.user-profile {
  position: relative;
  display: inline-block;
}

.user-profile__trigger {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 8px 12px;
  border-radius: 8px;
  transition: all 0.2s ease;
  cursor: pointer;
  user-select: none;

  &:hover {
    background: rgba(0, 0, 0, 0.05);
  }

  &:focus {
    outline: 2px solid #4285f4;
    outline-offset: 2px;
  }
}

.user-profile__avatar {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  overflow: hidden;
  flex-shrink: 0;
  position: relative;
}

.user-profile__avatar-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

.user-profile__avatar-fallback {
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, #4285f4, #34a853);
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 600;
  font-size: 16px;
}

.user-profile__info {
  display: flex;
  flex-direction: column;
  min-width: 0;
  flex: 1;
}

.user-profile__name {
  font-weight: 500;
  font-size: 14px;
  color: #202124;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.user-profile__email {
  font-size: 12px;
  color: #5f6368;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.user-profile__dropdown-icon {
  flex-shrink: 0;
  color: #5f6368;
  transition: transform 0.2s ease;

  &--open {
    transform: rotate(180deg);
  }
}

.user-profile__dropdown {
  position: absolute;
  top: 100%;
  right: 0;
  z-index: 1000;
  margin-top: 8px;
  background: white;
  border: 1px solid #dadce0;
  border-radius: 8px;
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.1);
  min-width: 280px;
  animation: dropdownFadeIn 0.15s ease-out;
}

.user-profile__dropdown-content {
  padding: 16px;
}

.user-profile__dropdown-section {
  margin-bottom: 16px;
}

.user-profile__dropdown-label {
  font-size: 12px;
  color: #5f6368;
  margin-bottom: 8px;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.user-profile__dropdown-user {
  font-size: 14px;
  line-height: 1.4;
}

.user-profile__dropdown-email {
  color: #5f6368;
  font-size: 12px;
}

.user-profile__dropdown-separator {
  border: none;
  border-top: 1px solid #e8eaed;
  margin: 16px 0;
}

.user-profile__logout-button {
  display: flex;
  align-items: center;
  gap: 8px;
  width: 100%;
  padding: 8px 12px;
  border: none;
  background: transparent;
  color: #d93025;
  font-size: 14px;
  border-radius: 4px;
  cursor: pointer;
  transition: all 0.2s ease;

  &:hover:not(:disabled) {
    background: rgba(217, 48, 37, 0.08);
  }

  &:disabled {
    opacity: 0.6;
    cursor: not-allowed;
  }

  svg {
    flex-shrink: 0;
  }
}

// Animaciones
@keyframes dropdownFadeIn {
  from {
    opacity: 0;
    transform: translateY(-8px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

// Responsivo
@media (max-width: 768px) {
  .user-profile__dropdown {
    left: 0;
    right: 0;
    min-width: auto;
  }

  .user-profile__info {
    display: none;
  }

  .user-profile__avatar {
    width: 32px;
    height: 32px;
  }

  .user-profile__avatar-fallback {
    font-size: 14px;
  }
}

// Tema oscuro
@media (prefers-color-scheme: dark) {
  .user-profile__trigger:hover {
    background: rgba(255, 255, 255, 0.08);
  }

  .user-profile__name {
    color: #e8eaed;
  }

  .user-profile__email {
    color: #9aa0a6;
  }

  .user-profile__dropdown-icon {
    color: #9aa0a6;
  }

  .user-profile__dropdown {
    background: #2d2e30;
    border-color: #5f6368;
  }

  .user-profile__dropdown-label {
    color: #9aa0a6;
  }

  .user-profile__dropdown-email {
    color: #9aa0a6;
  }

  .user-profile__dropdown-separator {
    border-color: #5f6368;
  }

  .user-profile__logout-button:hover:not(:disabled) {
    background: rgba(217, 48, 37, 0.12);
  }
}