/**
 * Estilos para el Panel de PONITIVOX
 * Basado en la estructura del StudyPanel con tema propio
 */

.ponitivox-panel {
  position: fixed;
  width: 340px;
  min-height: 400px;
  max-height: 80vh;
  background: linear-gradient(145deg, #1a1a2e 0%, #16213e 100%);
  border: 1px solid rgba(255, 215, 0, 0.3);
  border-radius: 16px;
  box-shadow: 
    0 20px 40px rgba(0, 0, 0, 0.4),
    0 0 20px rgba(255, 215, 0, 0.1);
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  backdrop-filter: blur(10px);
  transition: all 0.3s ease;

  &.dragging {
    user-select: none;
    box-shadow: 
      0 25px 50px rgba(0, 0, 0, 0.5),
      0 0 30px rgba(255, 215, 0, 0.2);
    transform: scale(1.02);
  }

  &.repositioned {
    // Ajustes específicos cuando hay otros paneles abiertos
    box-shadow: 
      0 15px 35px rgba(0, 0, 0, 0.35),
      0 0 15px rgba(255, 215, 0, 0.08);
  }

  // En móviles, usar posición CSS fija (igual que StudyPanel)
  &.mobile {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    max-height: 100vh;
    border-radius: 0;
    border: none;
    z-index: 10000;
    background: linear-gradient(180deg, #1a1a2e 0%, #16213e 100%);
  }
}

.ponitivox-panel-header {
  background: linear-gradient(90deg, rgba(255, 215, 0, 0.15) 0%, rgba(255, 193, 7, 0.1) 100%);
  border-bottom: 1px solid rgba(255, 215, 0, 0.2);
  padding: 16px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: move;
  position: relative;

  .panel-title {
    display: flex;
    align-items: center;
    gap: 12px;
    
    .panel-icon {
      font-size: 20px;
      filter: drop-shadow(0 0 8px rgba(255, 215, 0, 0.5));
    }

    h3 {
      color: #ffd700;
      font-size: 18px;
      font-weight: 700;
      margin: 0;
      text-shadow: 0 0 10px rgba(255, 215, 0, 0.3);
      letter-spacing: 1px;
    }
  }

  .close-button {
    background: rgba(255, 255, 255, 0.1);
    border: 1px solid rgba(255, 255, 255, 0.2);
    border-radius: 8px;
    color: #ffffff;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 8px;
    transition: all 0.2s ease;

    &:hover {
      background: rgba(255, 69, 58, 0.2);
      border-color: rgba(255, 69, 58, 0.4);
      color: #ff453a;
      transform: scale(1.1);
    }

    svg {
      width: 16px;
      height: 16px;
    }
  }

  // En móvil, hacer el header menos alto
  .mobile & {
    padding: 20px;
    cursor: default;
  }
}

.ponitivox-panel-content {
  flex: 1;
  padding: 20px;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  gap: 24px;

  // Scrollbar personalizado
  &::-webkit-scrollbar {
    width: 6px;
  }

  &::-webkit-scrollbar-track {
    background: rgba(255, 255, 255, 0.05);
    border-radius: 3px;
  }

  &::-webkit-scrollbar-thumb {
    background: rgba(255, 215, 0, 0.3);
    border-radius: 3px;
    
    &:hover {
      background: rgba(255, 215, 0, 0.5);
    }
  }
}

.ponitivox-main {
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.coming-soon-section {
  text-align: center;
  padding: 24px;
  background: rgba(255, 215, 0, 0.05);
  border: 1px solid rgba(255, 215, 0, 0.15);
  border-radius: 12px;

  .coming-soon-icon {
    margin-bottom: 16px;
    
    span {
      font-size: 48px;
      display: block;
      filter: drop-shadow(0 0 15px rgba(255, 215, 0, 0.4));
      animation: pulse-glow 2s infinite;
    }
  }

  h4 {
    color: #ffd700;
    font-size: 24px;
    font-weight: 600;
    margin: 0 0 12px 0;
    text-shadow: 0 0 10px rgba(255, 215, 0, 0.3);
  }

  p {
    color: rgba(255, 255, 255, 0.8);
    font-size: 16px;
    line-height: 1.5;
    margin: 0;
  }
}

.user-section {
  .user-login-btn {
    width: 100%;
    padding: 16px 20px;
    background: linear-gradient(135deg, rgba(255, 215, 0, 0.1) 0%, rgba(255, 193, 7, 0.05) 100%);
    border: 2px solid rgba(255, 215, 0, 0.2);
    border-radius: 12px;
    color: rgba(255, 255, 255, 0.6);
    font-size: 16px;
    font-weight: 500;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 12px;
    cursor: not-allowed;
    transition: all 0.3s ease;
    position: relative;
    overflow: hidden;

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: -100%;
      width: 100%;
      height: 100%;
      background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.1), transparent);
      transition: left 0.5s ease;
    }

    &.coming-soon-btn::before {
      animation: shimmer 3s infinite;
    }

    .user-icon {
      width: 20px;
      height: 20px;
      opacity: 0.6;
    }
  }

  .user-hint {
    margin-top: 12px;
    color: rgba(255, 255, 255, 0.5);
    font-size: 14px;
    text-align: center;
    line-height: 1.4;
  }
}

.features-preview {
  h5 {
    color: #ffffff;
    font-size: 16px;
    font-weight: 600;
    margin: 0 0 16px 0;
  }

  .features-list {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: column;
    gap: 12px;

    li {
      color: rgba(255, 255, 255, 0.7);
      font-size: 14px;
      padding: 12px 16px;
      background: rgba(255, 255, 255, 0.05);
      border: 1px solid rgba(255, 255, 255, 0.1);
      border-radius: 8px;
      transition: all 0.2s ease;

      &:hover {
        background: rgba(255, 215, 0, 0.08);
        border-color: rgba(255, 215, 0, 0.2);
        color: rgba(255, 255, 255, 0.9);
        transform: translateX(4px);
      }
    }
  }
}

// Animaciones
@keyframes pulse-glow {
  0%, 100% {
    filter: drop-shadow(0 0 15px rgba(255, 215, 0, 0.4));
    transform: scale(1);
  }
  50% {
    filter: drop-shadow(0 0 25px rgba(255, 215, 0, 0.6));
    transform: scale(1.05);
  }
}

@keyframes shimmer {
  0% {
    left: -100%;
  }
  100% {
    left: 100%;
  }
}

// Responsive para móviles
@media (max-width: 768px) {
  .ponitivox-panel {
    &.mobile {
      .ponitivox-panel-content {
        padding: 24px 20px;
      }

      .coming-soon-section {
        padding: 32px 20px;
        
        .coming-soon-icon span {
          font-size: 64px;
        }

        h4 {
          font-size: 28px;
        }

        p {
          font-size: 18px;
        }
      }

      .user-section .user-login-btn {
        padding: 20px;
        font-size: 18px;
      }
    }
  }
}

// Modo oscuro mejorado
.ponitivox-panel {
  // Efectos de hover mejorados
  &:hover {
    box-shadow: 
      0 25px 50px rgba(0, 0, 0, 0.5),
      0 0 25px rgba(255, 215, 0, 0.15);
  }
}