
/* Variables responsivas para audio-pulse - Optimizado para móviles */
:root {
  --audio-pulse-width: 24px;
  --audio-pulse-bar-width: 4px;
  --audio-pulse-min-height: 4px;
  --audio-pulse-hover-translate: -3.5px;
  
  @media (max-width: 768px) {
    --audio-pulse-width: 20px;
    --audio-pulse-bar-width: 3px;
    --audio-pulse-min-height: 3px;
    --audio-pulse-hover-translate: -2px;
  }
  
  @media (max-width: 480px) {
    --audio-pulse-width: 18px;
    --audio-pulse-bar-width: 2px;
    --audio-pulse-min-height: 2px;
    --audio-pulse-hover-translate: -1px;
  }
}

.audioPulse {
  display: flex;
  width: var(--audio-pulse-width);
  justify-content: space-evenly;
  align-items: center;
  transition: all 0.3s ease;
  /* Hacer más compacto en móviles */
  min-height: 20px;
  
  @media (max-width: 768px) {
    /* Compacto y centrado en móviles */
    justify-content: center;
    gap: 1px;
    min-height: 16px;
    /* Posición absoluta para overlay en lugar de tomar espacio */
    position: absolute;
    top: 8px;
    right: 8px;
    background: rgba(0, 0, 0, 0.7);
    border-radius: 8px;
    padding: 4px 6px;
    z-index: 10;
  }
  
  @media (max-width: 480px) {
    top: 6px;
    right: 6px;
    padding: 3px 5px;
    border-radius: 6px;
  }

  & > div {
    background-color: var(--Neutral-30);
    border-radius: 1000px;
    width: var(--audio-pulse-bar-width);
    min-height: var(--audio-pulse-min-height);
    border-radius: 1000px;
    transition: height 0.1s;
    
    @media (max-width: 768px) {
      /* Más visible con fondo oscuro */
      background-color: rgba(255, 255, 255, 0.8);
      transition: height 0.1s, background-color 0.2s;
    }
  }

  &.hover > div {
    animation: hover 1.4s infinite alternate ease-in-out;
    
    @media (max-width: 768px) {
      /* Animación más pronunciada en móviles */
      animation: hoverMobile 1.2s infinite alternate ease-in-out;
    }
  }

  height: var(--audio-pulse-min-height);
  transition: opacity 0.333s;

      &.active {
    opacity: 1;

    & > div {
      background-color: var(--Neutral-80);
      
      @media (max-width: 768px) {
        /* Color más intenso para overlay en móviles con mejor visibilidad */
        background-color: rgba(255, 255, 255, 0.9);
        box-shadow: 0 0 4px rgba(255, 255, 255, 0.8);
      }
    }
  }
  
  @media (max-width: 768px) {
    /* Posicionamiento absoluto para no interferir */
    position: fixed !important;
    top: 10px;
    right: 10px;
    z-index: 999;
    background: rgba(0, 0, 0, 0.8);
    border-radius: 8px;
    padding: 4px 8px;
    pointer-events: none; /* No interfiere con otros elementos */
    
    /* Estados de hover mejorados para touch */
    &:active > div {
      background-color: var(--Neutral-90);
      transform: scale(1.1);
    }
  }
}

@keyframes hover {
  from {
    transform: translateY(0);
  }

  to {
    transform: translateY(var(--audio-pulse-hover-translate));
  }
}

@keyframes hoverMobile {
  from {
    transform: translateY(0) scale(1);
  }

  to {
    transform: translateY(var(--audio-pulse-hover-translate)) scale(1.1);
  }
}

@keyframes pulse {
  from {
    scale: 1 1;
  }

  to {
    scale: 1.2 1.2;
  }
}
