/**
 * Optimizaciones específicas para Samsung S6 Edge y dispositivos ultra-pequeños
 * Dimensiones Samsung S6 Edge: 360x640px
 * Estas optimizaciones aseguran una experiencia óptima en dispositivos con pantallas muy pequeñas
 */

/* ===== OPTIMIZACIONES SAMSUNG S6 EDGE ===== */
@media screen and (max-width: 360px) {
  /* Variables globales optimizadas */
  :root {
    --s6-touch-target: 52px;
    --s6-padding: 8px;
    --s6-gap: 6px;
    --s6-border-radius: 8px;
    --s6-font-size-small: 11px;
    --s6-font-size-normal: 13px;
    --s6-font-size-button: 14px;
    --s6-font-size-heading: 16px;
  }

  /* Mejoras generales de legibilidad */
  body {
    font-size: var(--s6-font-size-normal);
    line-height: 1.4;
  }

  /* Botones más accesibles */
  button, 
  .action-button,
  [role="button"],
  input[type="button"],
  input[type="submit"] {
    min-height: var(--s6-touch-target);
    min-width: var(--s6-touch-target);
    font-size: var(--s6-font-size-button);
    padding: 8px 12px;
    border-radius: var(--s6-border-radius);
    
    /* Asegurar que el texto sea legible */
    font-weight: 500;
    letter-spacing: 0.3px;
  }

  /* Encabezados compactos pero legibles */
  h1, h2, h3, h4, h5, h6 {
    font-size: var(--s6-font-size-heading);
    line-height: 1.2;
    margin: var(--s6-gap) 0;
  }

  /* Inputs más grandes para mejor experiencia táctil */
  input, 
  textarea, 
  select {
    min-height: var(--s6-touch-target);
    font-size: 16px; /* Evitar zoom en iOS */
    padding: 10px 12px;
    border-radius: var(--s6-border-radius);
  }

  /* Contenedores con padding optimizado */
  .container,
  .content,
  .panel {
    padding: var(--s6-padding);
    gap: var(--s6-gap);
  }

  /* Optimizaciones específicas para elementos de la app */
  .streaming-console {
    .main-content {
      padding: var(--s6-padding);
      gap: var(--s6-gap);
    }
  }

  /* Side panel ultra-optimizado */
  .side-panel {
    &.open {
      width: 100vw;
      
      .side-panel-container {
        padding: var(--s6-padding);
      }
    }

    .input-container {
      min-height: 60px;
      padding: var(--s6-padding);
      
      .input-content {
        border-radius: var(--s6-border-radius);
        min-height: 44px;
      }
    }
  }

  /* Control tray optimizado */
  .control-tray {
    padding: var(--s6-padding);
    gap: var(--s6-gap);
    max-width: calc(100vw - 16px);
  }

  /* YouTube player compacto */
  .youtube-floating-player {
    margin: var(--s6-padding);
    border-radius: var(--s6-border-radius);
    
    .floating-header {
      padding: var(--s6-padding);
      min-height: 40px;
    }
    
    .floating-content {
      padding: var(--s6-padding);
    }
  }

  /* Audio pulse más visible */
  .audioPulse {
    width: 32px;
    
    & > div {
      width: 5px;
      min-height: 5px;
    }
  }

  /* Logger más compacto */
  .logger {
    font-size: var(--s6-font-size-small);
    
    li {
      padding: 4px 0;
      min-height: 36px;
    }
  }

  /* Welcome screen optimizado */
  .welcome-screen {
    padding: var(--s6-padding);
    
    .welcome-content {
      gap: var(--s6-gap);
    }
    
    .feature-item {
      padding: var(--s6-padding);
      gap: var(--s6-gap);
    }
  }

  /* Settings dialog compacto */
  .settings-dialog {
    .dialog {
      width: 95vw;
      margin: var(--s6-padding);
      border-radius: var(--s6-border-radius);
    }
  }
}

/* ===== LANDSCAPE ULTRA-COMPACTO ===== */
@media screen and (max-height: 400px) and (orientation: landscape) and (max-width: 640px) {
  :root {
    --landscape-touch-target: 40px;
    --landscape-padding: 4px;
    --landscape-gap: 4px;
  }

  body {
    font-size: 12px;
  }

  button, 
  .action-button {
    min-height: var(--landscape-touch-target);
    min-width: var(--landscape-touch-target);
    font-size: 12px;
    padding: 4px 8px;
  }

  .streaming-console {
    .main-content {
      padding: var(--landscape-padding);
      gap: var(--landscape-gap);
    }
    
    .control-area {
      height: 50px;
      padding: var(--landscape-padding);
    }
  }

  .side-panel {
    &.open {
      width: 50vw;
      max-width: 280px;
    }
  }
}

/* ===== MEJORAS DE RENDIMIENTO PARA DISPOSITIVOS LIMITADOS ===== */
@media screen and (max-width: 360px) {
  /* Reducir animaciones complejas */
  * {
    animation-duration: 0.2s !important;
    transition-duration: 0.2s !important;
  }

  /* Simplificar efectos de blur */
  .youtube-floating-player,
  .control-area {
    backdrop-filter: blur(5px);
    -webkit-backdrop-filter: blur(5px);
  }

  /* Optimizar sombras */
  .youtube-floating-player,
  .side-panel {
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.15);
  }
}

/* ===== OPTIMIZACIONES DE SCROLL PARA PANTALLAS PEQUEÑAS ===== */
@media screen and (max-width: 360px) {
  /* Scroll más suave y visible */
  .side-panel-container,
  .logger,
  .youtube-floating-player .floating-content {
    scrollbar-width: thin;
    scrollbar-color: var(--Neutral-50) var(--Neutral-10);
    
    &::-webkit-scrollbar {
      width: 6px;
    }
    
    &::-webkit-scrollbar-thumb {
      background: var(--Neutral-50);
      border-radius: 3px;
    }
    
    &::-webkit-scrollbar-track {
      background: var(--Neutral-10);
    }
  }
}
