/* Estilos para el componente MicrophoneStatus */

.microphone-status {
  position: fixed;
  top: 20px;
  right: 20px;
  max-width: 400px;
  min-width: 300px;
  background: #2a2a2a;
  border-radius: 12px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
  z-index: 1000;
  animation: slideIn 0.3s ease-out;
  border-left: 4px solid #4285f4;

  @media (max-width: 480px) {
    position: fixed;
    top: 10px;
    left: 10px;
    right: 10px;
    max-width: none;
    min-width: auto;
  }

  &.info {
    border-left-color: #ff9800;
    
    .status-icon {
      color: #ff9800;
    }
  }

  &.success {
    border-left-color: #34a853;
    
    .status-icon {
      color: #34a853;
    }
  }

  &.error {
    border-left-color: #ea4335;
    
    .status-icon {
      color: #ea4335;
    }
  }
}

.status-header {
  display: flex;
  align-items: flex-start;
  padding: 16px;
  gap: 12px;
}

.status-icon {
  font-size: 24px;
  flex-shrink: 0;
  margin-top: 2px;
}

.status-text {
  flex: 1;
  color: #ffffff;

  h3 {
    margin: 0 0 4px 0;
    font-size: 16px;
    font-weight: 600;
  }

  p {
    margin: 0;
    font-size: 14px;
    color: #b3b3b3;
    line-height: 1.4;
  }
}

.close-btn {
  background: none;
  border: none;
  color: #b3b3b3;
  font-size: 18px;
  cursor: pointer;
  padding: 4px;
  border-radius: 4px;
  transition: all 0.2s ease;
  flex-shrink: 0;

  &:hover {
    background: rgba(255, 255, 255, 0.1);
    color: #ffffff;
  }
}

.help-content {
  padding: 0 16px 16px 16px;
  border-top: 1px solid #404040;
  margin-top: 12px;
  padding-top: 16px;

  h4 {
    margin: 0 0 12px 0;
    color: #ffffff;
    font-size: 14px;
    font-weight: 600;
  }

  ol {
    margin: 0;
    padding-left: 20px;
    color: #b3b3b3;
    font-size: 13px;

    li {
      margin-bottom: 8px;
      line-height: 1.4;

      strong {
        color: #ffffff;
      }

      ul {
        margin: 4px 0 0 0;
        padding-left: 16px;

        li {
          margin-bottom: 4px;
        }
      }
    }
  }
}

.action-buttons {
  display: flex;
  gap: 8px;
  padding: 0 16px 16px 16px;

  button {
    flex: 1;
    padding: 8px 16px;
    border: none;
    border-radius: 6px;
    font-size: 14px;
    font-weight: 500;
    cursor: pointer;
    transition: all 0.2s ease;

    &.retry-btn {
      background: #4285f4;
      color: white;

      &:hover {
        background: #3367d6;
      }
    }
  }
}

.loading-indicator {
  padding: 0 16px 16px 16px;
  display: flex;
  justify-content: center;
}

.loading-dots {
  display: flex;
  gap: 4px;

  span {
    width: 6px;
    height: 6px;
    background: #4285f4;
    border-radius: 50%;
    animation: loadingPulse 1.4s infinite ease-in-out both;

    &:nth-child(1) { animation-delay: -0.32s; }
    &:nth-child(2) { animation-delay: -0.16s; }
    &:nth-child(3) { animation-delay: 0s; }
  }
}

@keyframes slideIn {
  from {
    transform: translateX(100%);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}

@keyframes loadingPulse {
  0%, 80%, 100% {
    transform: scale(0);
    opacity: 0.5;
  }
  40% {
    transform: scale(1);
    opacity: 1;
  }
}

/* Estilos para modo oscuro */
@media (prefers-color-scheme: dark) {
  .microphone-status {
    background: #1a1a1a;
    box-shadow: 0 8px 32px rgba(0, 0, 0, 0.5);
  }

  .help-content {
    border-top-color: #333333;
  }

}

/* Estilos para modo claro */
@media (prefers-color-scheme: light) {
  .microphone-status {
    background: #ffffff;
    color: #333333;
    box-shadow: 0 8px 32px rgba(0, 0, 0, 0.15);
  }

  .status-text {
    color: #333333;

    p {
      color: #666666;
    }
  }

  .close-btn {
    color: #666666;

    &:hover {
      background: rgba(0, 0, 0, 0.1);
      color: #333333;
    }
  }

  .help-content {
    border-top-color: #e0e0e0;

    h4 {
      color: #333333;
    }

    ol {
      color: #666666;

      li strong {
        color: #333333;
      }
    }
  }

  .action-buttons {
  }
}
