/* Importar fuentes */
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap');
@import url('https://fonts.googleapis.com/icon?family=Material+Icons');

/* Variables CSS */
:root {
  --gray-900: #111827;
  --gray-800: #1f2937;
  --gray-700: #374151;
  --gray-600: #4b5563;
  --gray-400: #9ca3af;
  --gray-300: #d1d5db;
  --blue-600: #2563eb;
  --blue-700: #1d4ed8;
  --blue-500: #3b82f6;
  --blue-400: #60a5fa;
  --pink-400: #f472b6;
  --purple-400: #c084fc;
  --yellow-400: #facc15;
  --green-400: #4ade80;
}

/* Reset y configuración base */
body {
  font-family: 'Roboto', sans-serif;
  background-color: var(--gray-900);
  min-height: max(884px, 100dvh);
}

.material-icons {
  font-family: 'Material Icons';
  font-weight: normal;
  font-style: normal;
  font-size: 24px;
  line-height: 1;
  letter-spacing: normal;
  text-transform: none;
  display: inline-block;
  white-space: nowrap;
  word-wrap: normal;
  direction: ltr;
  -webkit-font-feature-settings: 'liga';
  font-feature-settings: 'liga';
  -webkit-font-smoothing: antialiased;
  vertical-align: middle;
}

/* Contenedor principal */
.advanced-voice-selector {
  min-height: 100vh;
  padding: 1rem;
  background-color: var(--gray-900);
  color: white;
  font-family: 'Roboto', sans-serif;
  
  /* Scroll suave en móviles */
  overflow-x: hidden;
  -webkit-overflow-scrolling: touch;
}

/* Header del panel */
header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 1rem;
  border-bottom: 1px solid var(--gray-700);
  margin-bottom: 1.5rem;
  
  h1 {
    font-size: 1.25rem;
    font-weight: 700;
    margin: 0;
  }
  
  .material-icons {
    color: var(--gray-400);
  }
}

/* Botón toggle principal */
.voice-toggle-button {
  width: 100%;
  background-color: var(--blue-600);
  color: white;
  font-weight: 600;
  padding: 0.75rem 1rem;
  border-radius: 0.5rem;
  margin: 1.5rem 0;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease-in-out;
  border: none;
  cursor: pointer;
  
  &:hover {
    background-color: var(--blue-700);
  }
  
  .material-icons {
    margin-right: 0.5rem;
    font-size: 20px;
  }
}

/* Panel principal con scroll */
.voice-config-panel {
  background-color: var(--gray-800);
  padding: 1.5rem;
  border-radius: 1rem;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1);
  
  /* FORZAR scroll para mostrar todo el contenido */
  height: 500px;
  max-height: 500px;
  overflow-y: scroll !important;
  -webkit-overflow-scrolling: touch;
  
  /* Scroll bar más visible */
  &::-webkit-scrollbar {
    width: 12px;
    background-color: var(--gray-700);
  }
  
  &::-webkit-scrollbar-track {
    background: var(--gray-700);
    border-radius: 6px;
    margin: 2px;
  }
  
  &::-webkit-scrollbar-thumb {
    background: var(--blue-500);
    border-radius: 6px;
    border: 2px solid var(--gray-700);
    
    &:hover {
      background: var(--blue-400);
    }
  }
}

/* Header del panel de configuración */
.panel-header {
  display: flex;
  align-items: center;
  margin-bottom: 1.5rem;
  
  .material-icons {
    color: var(--pink-400);
    font-size: 2rem;
    margin-right: 0.75rem;
  }
  
  h2 {
    font-size: 1.25rem;
    font-weight: 700;
    margin: 0 0 0.25rem 0;
  }
  
  p {
    color: var(--gray-400);
    font-size: 0.875rem;
    margin: 0;
  }
}

/* Selector de voz */
.voice-selector-section {
  margin-bottom: 1.5rem;
  
  label {
    display: block;
    font-size: 0.875rem;
    font-weight: 500;
    color: var(--gray-300);
    margin-bottom: 0.5rem;
  }
  
  .voice-select-wrapper {
    position: relative;
    
    .select-icon {
      position: absolute;
      left: 0.75rem;
      top: 50%;
      transform: translateY(-50%);
      color: var(--purple-400);
      font-size: 20px;
    }
    
    .voice-select {
      width: 100%;
      background-color: var(--gray-700);
      border: 1px solid var(--gray-600);
      color: white;
      font-size: 0.875rem;
      border-radius: 0.5rem;
      padding: 0.75rem 0.75rem 0.75rem 2.5rem;
      appearance: none;
      cursor: pointer;
      
      &:focus {
        outline: none;
        box-shadow: 0 0 0 2px var(--blue-500);
        border-color: var(--blue-500);
      }
      
      option {
        background-color: var(--gray-700);
        color: white;
      }
    }
  }
}

/* Configuración actual */
.current-config-section {
  background: rgba(55, 65, 81, 0.5);
  padding: 1rem;
  border-radius: 0.75rem;
  margin-bottom: 1.5rem;
  
  h3 {
    font-size: 1.125rem;
    font-weight: 600;
    margin-bottom: 1rem;
    display: flex;
    align-items: center;
    
    .material-icons {
      color: var(--pink-400);
      margin-right: 0.5rem;
    }
  }
  
  .config-items {
    display: flex;
    flex-direction: column;
    gap: 0.75rem;
  }
  
  .config-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    
    .config-label {
      color: var(--gray-400);
    }
    
    .config-value {
      display: flex;
      align-items: center;
      font-weight: 500;
      
      .config-icon {
        margin-right: 0.5rem;
        font-size: 18px;
        
        &.text-yellow-400 { color: var(--yellow-400); }
        &.text-blue-400 { color: var(--blue-400); }
        &.text-green-400 { color: var(--green-400); }
      }
    }
  }
}

/* Botón aplicar */
.apply-button {
  width: 100% !important;
  background-color: var(--blue-500) !important;
  color: white !important;
  font-weight: 700;
  padding: 1rem 1rem;
  border-radius: 9999px;
  display: flex !important;
  align-items: center;
  justify-content: center;
  font-size: 1.125rem;
  transition: all 0.3s ease-in-out;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1);
  border: none;
  cursor: pointer;
  margin: 1rem 0 1rem 0;
  
  /* Asegurar visibilidad máxima */
  position: relative !important;
  z-index: 100 !important;
  opacity: 1 !important;
  visibility: visible !important;
  
  /* Indicador visual de scroll */
  &:hover {
    background-color: var(--blue-600) !important;
    transform: translateY(-1px);
    box-shadow: 0 6px 8px -1px rgba(0, 0, 0, 0.15);
  }
  
  .material-icons {
    margin-right: 0.5rem;
    font-size: 1.2rem;
  }
}

/* Consejo final */
.voice-tip {
  display: flex;
  align-items: flex-start;
  font-size: 0.875rem;
  color: var(--gray-400);
  
  .tip-icon {
    color: var(--yellow-400);
    margin-right: 0.5rem;
    margin-top: 0.125rem;
    font-size: 18px;
  }
  
  p {
    margin: 0;
    line-height: 1.4;
  }
}

/* Responsive para móviles */
@media screen and (max-width: 768px) {
  .advanced-voice-selector {
    padding: 0.75rem;
  }
  
  .voice-config-panel {
    padding: 1rem;
    height: 70vh;
    max-height: 70vh;
    border-radius: 0.75rem;
    overflow-y: scroll !important;
    
    /* Mejor scroll en móviles */
    -webkit-overflow-scrolling: touch;
    
    /* Scrollbar más visible en móviles */
    &::-webkit-scrollbar {
      width: 12px;
    }
    
    &::-webkit-scrollbar-thumb {
      background: var(--gray-400);
      border-radius: 6px;
    }
  }
  
  .panel-header {
    margin-bottom: 1rem;
    
    .material-icons {
      font-size: 1.75rem;
    }
    
    h2 {
      font-size: 1.125rem;
    }
    
    p {
      font-size: 0.75rem;
    }
  }
  
  .voice-selector-section {
    margin-bottom: 1rem;
  }
  
  .current-config-section {
    margin-bottom: 1rem;
  }
  
  .config-item {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.25rem;
    
    .config-value {
      align-self: flex-end;
    }
  }
}

/* Extra small devices */
@media screen and (max-width: 480px) {
  .advanced-voice-selector {
    padding: 0.5rem;
  }
  
  .voice-config-panel {
    padding: 0.75rem;
    height: 75vh;
    max-height: 75vh;
    overflow-y: scroll !important;
    
    /* Scrollbar aún más visible en pantallas pequeñas */
    &::-webkit-scrollbar {
      width: 14px;
    }
    
    &::-webkit-scrollbar-thumb {
      background: var(--blue-400);
      border-radius: 7px;
    }
  }
  
  .voice-toggle-button,
  .apply-button {
    padding: 0.625rem 0.75rem;
    font-size: 0.875rem;
  }
  
  .panel-header {
    margin-bottom: 0.75rem;
    
    h2 {
      font-size: 1rem;
    }
  }
  
  .voice-selector-section,
  .current-config-section {
    margin-bottom: 0.75rem;
  }
}

/* Pantallas extra pequeñas (teléfonos pequeños) */
@media screen and (max-width: 360px) {
  .voice-config-panel {
    height: 80vh;
    max-height: 80vh;
    padding: 0.5rem;
    
    /* Scrollbar muy visible en pantallas tiny */
    &::-webkit-scrollbar {
      width: 16px;
    }
    
    &::-webkit-scrollbar-thumb {
      background: var(--blue-500);
      border-radius: 8px;
    }
  }
  
  .panel-header {
    margin-bottom: 0.5rem;
    
    h2 {
      font-size: 0.9rem;
    }
    
    p {
      font-size: 0.7rem;
    }
  }
  
  .voice-selector-section,
  .current-config-section {
    margin-bottom: 0.5rem;
  }
  
  .apply-button {
    font-size: 0.8rem;
    padding: 0.5rem;
  }
  
  .voice-tip {
    font-size: 0.75rem;
  }
}
