.thinking-mode-toggle {
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

.toggle-button {
  background: transparent;
  border: none;
  padding: 8px 12px;
  border-radius: 20px;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  gap: 8px;
  position: relative;
  overflow: hidden;
  
  /* Área táctil mínima para móviles */
  min-height: 44px;
  min-width: 44px;
  
  /* Base styling */
  &.normal {
    background: rgba(108, 117, 125, 0.1);
    color: #6c757d;
    border: 1px solid rgba(108, 117, 125, 0.3);
    
    .bulb-glow {
      opacity: 0;
    }
    
    .bulb-icon {
      filter: grayscale(0.7);
    }
  }
  
  &.thinking {
    background: linear-gradient(135deg, #ffc107, #ff8c00);
    color: #fff;
    border: 1px solid #ff8c00;
    box-shadow: 0 4px 15px rgba(255, 193, 7, 0.4);
    
    .bulb-glow {
      opacity: 1;
      animation: pulse-glow 2s ease-in-out infinite;
    }
    
    .bulb-icon {
      filter: none;
      animation: think-pulse 1.5s ease-in-out infinite;
    }
  }
  
  /* Hover effects */
  @media (hover: hover) {
    &:hover {
      transform: translateY(-2px);
      
      &.normal {
        background: rgba(108, 117, 125, 0.2);
        box-shadow: 0 4px 10px rgba(108, 117, 125, 0.2);
      }
      
      &.thinking {
        box-shadow: 0 6px 20px rgba(255, 193, 7, 0.6);
      }
    }
  }
  
  /* Touch feedback for mobile */
  @media (hover: none) {
    &:active {
      transform: scale(0.95);
    }
  }
}

.bulb-container {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 24px;
  height: 24px;
}

.bulb-icon {
  font-size: 20px;
  transition: all 0.3s ease;
  z-index: 2;
  position: relative;
}

.bulb-glow {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 30px;
  height: 30px;
  background: radial-gradient(circle, rgba(255, 193, 7, 0.6) 0%, transparent 70%);
  border-radius: 50%;
  opacity: 0;
  transition: opacity 0.3s ease;
  z-index: 1;
}

.mode-text {
  font-size: 11px;
  font-weight: 600;
  letter-spacing: 0.5px;
  text-transform: uppercase;
  white-space: nowrap;
  
  /* Ocultar texto en pantallas muy pequeñas */
  @media (max-width: 480px) {
    display: none;
  }
}

/* Animaciones */
@keyframes pulse-glow {
  0%, 100% {
    transform: translate(-50%, -50%) scale(1);
    opacity: 0.6;
  }
  50% {
    transform: translate(-50%, -50%) scale(1.2);
    opacity: 0.8;
  }
}

@keyframes think-pulse {
  0%, 100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
}

/* Adaptaciones móviles */
@media (max-width: 768px) {
  .thinking-mode-toggle {
    .toggle-button {
      padding: 6px 10px;
      border-radius: 16px;
      gap: 6px;
      
      .bulb-icon {
        font-size: 18px;
      }
      
      .mode-text {
        font-size: 10px;
      }
    }
  }
}

/* Versión compacta para el control tray */
.control-tray .thinking-mode-toggle {
  .toggle-button {
    width: var(--control-secondary-size, 44px);
    height: var(--control-secondary-size, 44px);
    padding: 0;
    min-width: var(--touch-target-min, 40px);
    min-height: var(--touch-target-min, 40px);
    border-radius: var(--control-border-radius, 50%);
    border: 2px solid var(--control-border, #606060);
    
    &.normal {
      background: var(--control-surface, #404040);
      color: var(--control-text-secondary, #b3b3b3);
      border-color: var(--control-border, #606060);
    }
    
    &.thinking {
      background: linear-gradient(135deg, var(--control-warning, #fbbc04), #ff8c00);
      color: var(--control-text, #ffffff);
      border-color: var(--control-warning, #fbbc04);
      box-shadow: var(--control-shadow, 0 2px 8px rgba(0,0,0,0.4));
    }
    
    .bulb-container {
      width: var(--icon-size, 20px);
      height: var(--icon-size, 20px);
    }
    
    .bulb-icon {
      font-size: var(--icon-size, 20px);
    }
    
    .mode-text {
      display: none; // Ocultar texto en el control tray para ahorrar espacio
    }
    
    @media (hover: hover) {
      &:hover {
        transform: scale(1.05);
        
        &.normal {
          background: var(--control-primary, #4285f4);
          border-color: var(--control-primary, #4285f4);
          color: var(--control-text, #ffffff);
        }
        
        &.thinking {
          box-shadow: 0 6px 16px rgba(251, 188, 4, 0.4);
        }
      }
    }
    
    &:active {
      transform: scale(0.95);
    }
    
    &:focus {
      outline: 2px solid var(--control-primary, #4285f4);
      outline-offset: 2px;
    }
  }
}

/* Dark mode support */
@media (prefers-color-scheme: dark) {
  .toggle-button {
    &.normal {
      background: rgba(255, 255, 255, 0.1);
      color: #e9ecef;
      border-color: rgba(255, 255, 255, 0.3);
    }
  }
}
