/**
 * Optimizaciones para Screen Sharing + YouTube en Móvil
 * Mejora la experiencia cuando se comparte pantalla mientras se reproduce un video de YouTube
 */

/* ===== VARIABLES ESPECÍFICAS PARA SCREEN SHARING + YOUTUBE ===== */
:root {
  --screen-share-overlay-bg: rgba(0, 0, 0, 0.8);
  --screen-share-controls-height: 60px;
  --youtube-mini-width: 140px;
  --youtube-mini-height: 80px;
  --transition-smooth: all 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

/* ===== MODO SCREEN SHARING CON YOUTUBE ACTIVO ===== */
.app-container.screen-sharing-active.youtube-active {
  
  /* Reorganizar layout cuando ambos están activos */
  &.mobile-layout {
      /* YouTube se convierte en mini-player flotante optimizado */
    .youtube-panel {
      position: fixed;
      top: 70px;
      right: 10px;
      width: var(--youtube-mini-width);
      height: var(--youtube-mini-height);
      z-index: 9999;
      background: rgba(0, 0, 0, 0.9);
      border: 1px solid rgba(255, 255, 255, 0.2);
      border-radius: 8px;
      box-shadow: 0 4px 20px rgba(0, 0, 0, 0.5);
      transition: var(--transition-smooth);
      
      /* Forzar comportamiento de mini cuando está en modo screen sharing */
      &.mini-mode {
        /* Ocultar header y controles no esenciales */
        .youtube-panel-header {
          display: none;
        }
        
        .youtube-panel-content {
          display: block !important;
          padding: 0;
          
          .search-form,
          .playback-controls {
            display: none;
          }
          
          .video-container {
            height: 100%;
            border-radius: 6px;
            overflow: hidden;
            
            .youtube-iframe {
              width: 100%;
              height: 100%;
              border-radius: 0;
            }
            
            .placeholder {
              height: 100%;
              display: flex;
              align-items: center;
              justify-content: center;
              background: rgba(255, 255, 255, 0.1);
              
              .placeholder-content {
                text-align: center;
                
                .placeholder-icon {
                  font-size: 24px;
                }
                
                p, small {
                  font-size: 10px;
                  margin: 2px 0;
                  color: rgba(255, 255, 255, 0.8);
                }
              }
            }
          }
        }
      }
      
      /* Asegurar que el video se mantenga en proporción */
      .youtube-iframe {
        width: 100%;
        height: 100%;
        border-radius: 6px;
      }
      
      /* Botón para expandir/cerrar en esquina */
      &::after {
        content: "×";
        position: absolute;
        top: -8px;
        right: -8px;
        width: 20px;
        height: 20px;
        background: rgba(255, 255, 255, 0.9);
        color: #000;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 14px;
        font-weight: bold;
        cursor: pointer;
        z-index: 1;
      }
      
      /* Hover/tap para mostrar más controles */
      &:hover,
      &:focus {
        width: calc(var(--youtube-mini-width) * 1.5);
        height: calc(var(--youtube-mini-height) * 1.5);
        
        &.mini-mode::before {
          content: "⤢";
          position: absolute;
          bottom: 4px;
          left: 4px;
          width: 24px;
          height: 24px;
          background: rgba(0, 0, 0, 0.7);
          color: white;
          border-radius: 4px;
          display: flex;
          align-items: center;
          justify-content: center;
          font-size: 12px;
          cursor: pointer;
          z-index: 1;
        }
      }
    }
    
    /* Screen sharing toma el área principal */
    .screen-sharing-display {
      position: fixed;
      top: 50px;
      left: 0;
      right: 0;
      bottom: var(--screen-share-controls-height);
      background: var(--screen-share-overlay-bg);
      z-index: 100;
      display: flex;
      align-items: center;
      justify-content: center;
      
      /* Contenedor del screen share */
      .screen-content {
        width: 100%;
        height: 100%;
        object-fit: contain;
        background: #000;
        border-radius: 0;
      }
      
      /* Indicador de que está compartiendo pantalla */
      .sharing-indicator {
        position: absolute;
        top: 10px;
        left: 10px;
        background: rgba(255, 0, 0, 0.8);
        color: white;
        padding: 4px 8px;
        border-radius: 4px;
        font-size: 12px;
        font-weight: bold;
        display: flex;
        align-items: center;
        gap: 4px;
        
        &::before {
          content: "●";
          animation: pulse 1.5s infinite;
        }
      }
    }
    
    /* Controles optimizados para este modo */
    .mobile-controls-footer {
      height: var(--screen-share-controls-height);
      background: rgba(0, 0, 0, 0.9);
      backdrop-filter: blur(20px);
      border-top: 1px solid rgba(255, 255, 255, 0.1);
      
      .control-tray {
        flex-direction: row;
        justify-content: space-around;
        align-items: center;
        
        /* Botones específicos para este modo */
        .screen-share-controls {
          display: flex;
          gap: 8px;
          align-items: center;
          
          .stop-sharing-btn {
            background: #ff4444;
            color: white;
            border: none;
            padding: 8px 16px;
            border-radius: 20px;
            font-size: 12px;
            font-weight: bold;
            cursor: pointer;
            transition: var(--transition-smooth);
            
            &:hover {
              background: #ff6666;
            }
          }
          
          .minimize-youtube-btn {
            background: rgba(255, 255, 255, 0.1);
            color: white;
            border: 1px solid rgba(255, 255, 255, 0.2);
            padding: 6px 12px;
            border-radius: 16px;
            font-size: 11px;
            cursor: pointer;
            transition: var(--transition-smooth);
            
            &:hover {
              background: rgba(255, 255, 255, 0.2);
            }
          }
        }
      }
    }
    
    /* Ocultar elementos que pueden interferir */
    .conversation-area {
      display: none;
    }
    
    .side-panel {
      z-index: 50; /* Menor prioridad que YouTube mini */
      
      &.open {
        background: rgba(0, 0, 0, 0.95);
        backdrop-filter: blur(20px);
      }
    }
  }
}

/* ===== OPTIMIZACIONES ESPECÍFICAS POR TAMAÑO ===== */

/* Samsung S6 Edge y ultra-pequeños */
@media screen and (max-width: 360px) {
  .app-container.screen-sharing-active.youtube-active.mobile-layout {
    
    .youtube-panel {
      width: 120px;
      height: 68px;
      top: 55px;
      right: 5px;
      
      &:hover,
      &:focus {
        width: 160px;
        height: 90px;
      }
    }
    
    .screen-sharing-display {
      top: 45px;
      
      .sharing-indicator {
        font-size: 10px;
        padding: 2px 6px;
      }
    }
    
    .mobile-controls-footer {
      height: 50px;
      
      .control-tray .screen-share-controls {
        gap: 4px;
        
        .stop-sharing-btn {
          padding: 6px 12px;
          font-size: 10px;
        }
        
        .minimize-youtube-btn {
          padding: 4px 8px;
          font-size: 9px;
        }
      }
    }
  }
}

/* iPhone y similares */
@media screen and (max-width: 390px) and (min-width: 361px) {
  .app-container.screen-sharing-active.youtube-active.mobile-layout {
    
    .youtube-panel {
      width: 130px;
      height: 75px;
      
      &:hover,
      &:focus {
        width: 170px;
        height: 95px;
      }
    }
  }
}

/* Landscape mode optimizations */
@media screen and (max-height: 500px) and (orientation: landscape) {
  .app-container.screen-sharing-active.youtube-active.mobile-layout {
    
    .youtube-panel {
      top: 10px;
      right: 10px;
      width: 160px;
      height: 90px;
      
      &:hover,
      &:focus {
        width: 200px;
        height: 112px;
      }
    }
    
    .screen-sharing-display {
      top: 0;
    }
    
    .mobile-header {
      height: 40px;
    }
    
    .mobile-controls-footer {
      height: 45px;
    }
  }
}

/* ===== MEJORAS ADICIONALES PARA INTERACCIÓN TÁCTIL ===== */

/* Asegurar que el mini-player sea accesible con gestos táctiles */
.app-container.screen-sharing-active.youtube-active.mobile-layout {
  
  .youtube-panel.mini-mode {
    /* Hacer el mini-player arrastrable */
    cursor: move;
    user-select: none;
    
    /* Gesture indicators */
    &::before {
      content: "⋮⋮";
      position: absolute;
      top: 2px;
      left: 50%;
      transform: translateX(-50%);
      color: rgba(255, 255, 255, 0.5);
      font-size: 8px;
      line-height: 1;
      letter-spacing: -1px;
    }
    
    /* Expandir área táctil para mejor UX */
    &:active {
      transform: scale(1.05);
    }
    
    /* Double tap para expandir */
    &.expanded-temporarily {
      width: calc(var(--youtube-mini-width) * 2);
      height: calc(var(--youtube-mini-height) * 2);
      z-index: 10000;
      
      .youtube-panel-content {
        .video-container::after {
          content: "Toca dos veces para minimizar";
          position: absolute;
          bottom: 0;
          left: 0;
          right: 0;
          background: rgba(0, 0, 0, 0.8);
          color: white;
          font-size: 10px;
          padding: 4px;
          text-align: center;
        }
      }
    }
  }
  
  /* Mejorar visibilidad del indicador de screen sharing */
  .screen-sharing-display {
    .sharing-indicator {
      /* Animación de pulso más suave */
      &::before {
        animation: pulse-smooth 2s infinite;
      }
      
      /* Agregar más información */
      &::after {
        content: " - Toca el mini-player para controlar YouTube";
        font-size: 10px;
        opacity: 0.8;
        margin-left: 4px;
      }
    }
    
    /* Mejorar contraste del contenido compartido */
    .screen-content {
      filter: brightness(1.1) contrast(1.05);
    }
  }
  
  /* Optimizar controles para acceso rápido */
  .mobile-controls-footer {
    .control-tray {
      .screen-share-controls {
        /* Botones más grandes para mejor accesibilidad */
        .stop-sharing-btn {
          min-width: 80px;
          position: relative;
          
          /* Indicador visual de que es importante */
          &::before {
            content: "";
            position: absolute;
            top: -2px;
            left: -2px;
            right: -2px;
            bottom: -2px;
            background: linear-gradient(45deg, #ff4444, #ff6666);
            border-radius: 22px;
            z-index: -1;
            animation: glow-warning 2s infinite;
          }
        }
        
        .minimize-youtube-btn {
          min-width: 60px;
          background: rgba(66, 133, 244, 0.2);
          border-color: rgba(66, 133, 244, 0.4);
          
          &:hover {
            background: rgba(66, 133, 244, 0.3);
          }
        }
      }
    }
  }
}

/* ===== ANIMACIONES ===== */
@keyframes pulse {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.5; }
}

@keyframes slideInMini {
  from {
    transform: translateX(100%) scale(0.8);
    opacity: 0;
  }
  to {
    transform: translateX(0) scale(1);
    opacity: 1;
  }
}

@keyframes pulse-smooth {
  0%, 100% { 
    opacity: 1; 
    transform: scale(1);
  }
  50% { 
    opacity: 0.7; 
    transform: scale(1.05);
  }
}

@keyframes glow-warning {
  0%, 100% { 
    opacity: 0.8; 
    box-shadow: 0 0 5px rgba(255, 68, 68, 0.5);
  }
  50% { 
    opacity: 1; 
    box-shadow: 0 0 15px rgba(255, 68, 68, 0.8);
  }
}

@keyframes mini-bounce {
  0%, 100% { transform: translateY(0); }
  50% { transform: translateY(-2px); }
}

/* ===== OPTIMIZACIONES ESPECÍFICAS PARA DIFERENTES ORIENTACIONES ===== */

/* Portrait mode - optimizado para uso vertical */
@media screen and (orientation: portrait) {
  .app-container.screen-sharing-active.youtube-active.mobile-layout {
    
    .youtube-panel.mini-mode {
      /* Posicionar en esquina superior derecha */
      top: 60px;
      right: 8px;
      
      /* Hacer ligeramente más grande en portrait */
      width: calc(var(--youtube-mini-width) * 1.1);
      height: calc(var(--youtube-mini-height) * 1.1);
    }
    
    .screen-sharing-display {
      /* Maximizar área de screen share en portrait */
      top: 50px;
      bottom: 70px;
      
      .sharing-indicator {
        top: 8px;
        left: 8px;
        font-size: 11px;
      }
    }
  }
}

/* Landscape mode - optimizado para uso horizontal */
@media screen and (orientation: landscape) {
  .app-container.screen-sharing-active.youtube-active.mobile-layout {
    
    .youtube-panel.mini-mode {
      /* Posicionar en esquina superior izquierda en landscape */
      top: 10px;
      right: 10px;
      left: auto;
      
      /* Tamaño más compacto en landscape */
      width: var(--youtube-mini-width);
      height: var(--youtube-mini-height);
    }
    
    .screen-sharing-display {
      top: 0;
      bottom: 55px;
      
      .sharing-indicator {
        top: 5px;
        left: 5px;
        font-size: 10px;
        padding: 2px 6px;
      }
    }
    
    .mobile-controls-footer {
      height: 55px;
      
      .control-tray .screen-share-controls {
        gap: 6px;
        
        .stop-sharing-btn {
          min-width: 70px;
          padding: 6px 12px;
        }
        
        .minimize-youtube-btn {
          min-width: 50px;
          padding: 4px 10px;
        }
      }
    }
  }
}

/* ===== MEJORAS DE ACCESIBILIDAD ===== */
.app-container.screen-sharing-active.youtube-active {
  
  /* Asegurar que los elementos focusables sean accesibles */
  .youtube-panel:focus {
    outline: 2px solid #4285f4;
    outline-offset: 2px;
  }
  
  .stop-sharing-btn:focus {
    outline: 2px solid white;
    outline-offset: 2px;
  }
  
  /* Indicadores visuales mejorados */
  .sharing-indicator {
    transform: translateZ(0); /* Forzar aceleración hardware */
  }
}

/* ===== OPTIMIZACIONES DE RENDIMIENTO ===== */
.app-container.screen-sharing-active.youtube-active {
  
  /* Optimizar rendering */
  .youtube-panel,
  .screen-sharing-display,
  .mobile-controls-footer {
    will-change: transform, opacity;
    transform: translateZ(0);
  }
  
  /* Reducir repaints innecesarios */
  .youtube-iframe {
    backface-visibility: hidden;
    -webkit-backface-visibility: hidden;
  }
  
  /* Optimizar compositing layers */
  .youtube-panel.mini-mode,
  .screen-sharing-display,
  .mobile-controls-footer {
    contain: layout style paint;
    transform: translateZ(0);
  }
  
  /* Preload de transiciones para mejor rendimiento */
  .youtube-panel.mini-mode {
    will-change: transform, opacity, width, height;
  }
  
  .screen-sharing-display {
    will-change: opacity;
  }
}

/* ===== FALLBACKS PARA DISPOSITIVOS ANTIGUOS ===== */
@supports not (backdrop-filter: blur(10px)) {
  .app-container.screen-sharing-active.youtube-active.mobile-layout {
    
    .mobile-controls-footer {
      background: rgba(0, 0, 0, 0.95);
    }
    
    .youtube-panel.mini-mode {
      background: rgba(0, 0, 0, 0.95);
    }
  }
}
