// VOXIA Mobile-First Layout - Estilo moderno
// Optimizado para uso vertical en móviles

:root {
  // Colores para mobile-first
  --mobile-bg: #212121;
  --mobile-surface: #2a2a2a;
  --mobile-header-bg: #1a1a1a;
  --mobile-text: #ffffff;
  --mobile-text-secondary: #b3b3b3;
  --mobile-accent: #4285f4;
  --mobile-success: #34a853;
  --mobile-border: #404040;
  
  // Layout mobile responsivo
  --mobile-header-height: 50px;
  --mobile-footer-height: 90px;
  --mobile-border-radius: 8px;
  --mobile-shadow: 0 1px 4px rgba(0,0,0,0.3);
  
  // Espaciado mobile ultra-compacto
  --mobile-padding: 12px;
  --mobile-gap: 8px;
  --mobile-touch-target: 44px;
  
  // Tipografía mobile optimizada
  --mobile-font-base: 14px;
  --mobile-font-small: 12px;
  --mobile-font-large: 16px;
  
  // iPhone XE y pantallas ultra-pequeñas (375px y menor)
  --ultra-small-padding: 6px;
  --ultra-small-gap: 4px;
  --ultra-small-touch-target: 40px;
  --ultra-small-font: 12px;
  --ultra-small-header-height: 45px;
  --ultra-small-footer-height: 80px;
  
  // iPhone 14 y similares (390px)
  --iphone-14-padding: 8px;
  --iphone-14-gap: 6px;
  --iphone-14-touch-target: 42px;
  --iphone-14-header-height: 48px;
  --iphone-14-footer-height: 85px;
}

// Reset y base
* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Space Mono", monospace;
  background: var(--mobile-bg);
  color: var(--mobile-text);
  overflow-x: hidden;
  -webkit-font-smoothing: antialiased;
  touch-action: manipulation;
}

// Layout principal mobile-first
.mobile-first-layout {
  width: 100vw;
  height: 100vh;
  height: 100dvh;
  overflow: hidden;
}

.mobile-chat-layout {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  background: var(--mobile-bg);
}

// Header compacto estilo ChatGPT
.mobile-header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: var(--mobile-header-height);
  background: var(--mobile-header-bg);
  border-bottom: 1px solid var(--mobile-border);
  z-index: 100;
  backdrop-filter: blur(10px);
  
  .header-content {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 100%;
    padding: 0 var(--mobile-padding);      .header-brand {
        display: flex;
        align-items: center;
        gap: 10px;
        
        .voxia-logo {
          width: 32px;
          height: 32px;
          object-fit: contain;
          filter: brightness(1.1);
          transition: transform 0.2s ease;
          
          &:hover {
            transform: scale(1.05);
          }
          
          @media screen and (max-width: 375px) {
            width: 28px;
            height: 28px;
          }
          
          @media screen and (max-width: 360px) {
            width: 24px;
            height: 24px;
          }
        }
      }
    
    .app-title {
      font-size: var(--mobile-font-large);
      font-weight: 600;
      margin: 0;
      color: var(--mobile-text);
    }
    
    .header-actions {
      display: flex;
      gap: var(--mobile-gap);
      
      .youtube-btn,
      .chatbot-vox-btn,
      .study-btn,
      .calendar-btn,
      .ponitivox-btn,
      .user-profile-btn,
      .control-panel-btn {
        width: var(--mobile-touch-target);
        height: var(--mobile-touch-target);
        border: none;
        border-radius: 8px; /* Todos con border radius consistente */
        background: transparent; /* Fondo transparente por defecto */
        color: var(--mobile-text);
        font-size: 20px;
        cursor: pointer;
        display: flex;
        align-items: center;
        justify-content: center;
        transition: background 0.2s ease;
        
        &:hover {
          background: var(--mobile-accent);
        }
        
        &:active {
          transform: scale(0.95);
        }
      }
      
      // Estilos específicos para el perfil de usuario en el header
      .header-user-profile {
        width: auto;
        height: auto;
        
        .user-profile__trigger {
          padding: 4px 8px;
          border-radius: 8px;
          background: transparent;
          transition: background 0.2s ease;
          
          &:hover {
            background: var(--mobile-accent);
          }
        }
        
        .user-profile__avatar {
          width: 32px;
          height: 32px;
        }
        
        .user-profile__info {
          display: none; // Ocultar información del usuario en móvil
        }
        
        .user-profile__dropdown-icon {
          display: none; // Ocultar icono de dropdown en móvil
        }
        
        .user-profile__dropdown {
          right: 0;
          left: auto;
          margin-top: 8px;
        }
      }
      
      /* SOLO cambiar la forma del chatbot a cuadrada, MANTENER todo lo demás igual */
      .chatbot-vox-btn {
        border-radius: 8px; /* Solo cambiar esto: de circular a cuadrado */
      }

      /* Estilo especial para PONITIVOX - sin gradiente, solo texto dorado */
      .ponitivox-btn {
        background: transparent;
        color: #ffd700; /* Texto dorado */
        
        &:hover {
          background: rgba(255, 215, 0, 0.1); /* Fondo sutil dorado al hover */
          color: #ffed4e;
        }
        
        &:active {
          transform: scale(0.95);
        }
      }

      /* Estilo especial para botón de perfil de usuario - sin gradiente, solo texto azul */
      .user-profile-btn {
        background: transparent;
        color: #4a90e2; /* Texto azul */
        
        &:hover {
          background: rgba(74, 144, 226, 0.1); /* Fondo sutil azul al hover */
          color: #63b3ed;
        }
        
        &:active {
          transform: scale(0.95);
        }
      }

      /* Estilo especial para botón del panel de control */
      .control-panel-btn {
        background: transparent;
        color: #888d8f; /* Color gris neutro */
        
        &:hover {
          background: rgba(136, 141, 143, 0.1); /* Fondo sutil gris al hover */
          color: #c3c6c7;
        }
        
        &:active {
          transform: scale(0.95);
        }
      }
    }
  }
}

// Área de conversación principal
.conversation-area {
  flex: 1;
  padding-top: var(--mobile-header-height);
  padding-bottom: var(--mobile-footer-height);
  overflow-y: auto;
  overflow-x: hidden;
  
  .chat-container {
    min-height: 100%;
    display: flex;
    flex-direction: column;
    padding: var(--mobile-padding);
    gap: var(--mobile-gap);
  }
}

// Video container móvil
.mobile-video-wrapper {
  width: 100%;
  max-width: 100%;
  border-radius: var(--mobile-border-radius);
  overflow: hidden;
  background: var(--mobile-surface);
  box-shadow: var(--mobile-shadow);
  
  .mobile-video {
    width: 100%;
    height: auto;
    display: block;
    border-radius: var(--mobile-border-radius);
  }
}

// Footer de controles estilo ChatGPT
.mobile-controls-footer {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  height: var(--mobile-footer-height);
  background: var(--mobile-header-bg);
  border-top: 1px solid var(--mobile-border);
  z-index: 100;
  backdrop-filter: blur(10px);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: var(--mobile-padding);
  
  // Asegurar que el ControlTray use todo el espacio disponible
  .control-tray {
    width: 100%;
    max-width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 8px;
    padding: 4px 8px;
    
    .actions-nav {
      width: 100%;
      max-width: 100%;
      flex: 1;
      display: flex;
      align-items: center;
      justify-content: space-evenly;
      flex-wrap: nowrap;
      
      // Distribución equitativa de botones
      > * {
        flex: 0 0 auto;
        margin: 0 2px;
      }
    }
    
    .connection-container {
      flex-shrink: 0;
      margin-top: 4px;
    }
  }
  
  // Optimizaciones para pantallas pequeñas
  @media screen and (max-width: 375px) {
    height: var(--ultra-small-footer-height);
    padding: var(--ultra-small-padding);
    
    .control-tray {
      gap: 4px;
      padding: 2px 4px;
      
      .actions-nav {
        > * {
          margin: 0 1px;
        }
      }
    }
  }
  
  // Samsung S6 Edge y ultra-pequeñas
  @media screen and (max-width: 360px) {
    .control-tray {
      .actions-nav {
        gap: 2px;
        padding: 4px;
        
        > * {
          margin: 0;
        }
      }
    }
  }
  
  // Modo horizontal
  @media screen and (max-height: 500px) and (orientation: landscape) {
    height: 60px;
    padding: 4px 8px;
    
    .control-tray {
      gap: 2px;
      padding: 2px 4px;
      
      .connection-container {
        margin-top: 2px;
        
        .text-indicator {
          display: none; // Ocultar texto en horizontal compacto
        }
      }
    }
  }
}

// Ocultar elementos no esenciales en móvil
.hidden-mobile {
  @media screen and (max-width: 768px) {
    display: none !important;
  }
}

.env-checker-container {
  position: absolute;
  top: 10px;
  right: 10px;
  z-index: 50;
  opacity: 0.7;
  font-size: 12px;
}

// Media queries para diferentes tamaños
@media screen and (max-width: 480px) {
  :root {
    --mobile-padding: 12px;
    --mobile-gap: 8px;
    --mobile-font-base: 15px;
  }
  
  .mobile-header .header-content .app-title {
    font-size: 16px;
  }
  
  .conversation-area .chat-container {
    padding: 12px;
  }
}

// iPhone 14 y similares (390px)
@media screen and (max-width: 390px) {
  :root {
    --mobile-padding: var(--iphone-14-padding);
    --mobile-gap: var(--iphone-14-gap);
    --mobile-touch-target: var(--iphone-14-touch-target);
    --mobile-header-height: var(--iphone-14-header-height);
    --mobile-footer-height: var(--iphone-14-footer-height);
  }
  
  .mobile-header {
    height: var(--iphone-14-header-height);
    
    .header-content {
      padding: 0 var(--iphone-14-padding);
      
      .app-title {
        font-size: 14px;
      }
      
      .youtube-btn,
      .chatbot-vox-btn,
      .study-btn,
      .calendar-btn,
      .ponitivox-btn,
      .user-profile-btn,
      .control-panel-btn {
        width: 36px;
        height: 36px;
        font-size: 14px;
      }
      
      .chatbot-vox-btn {
        border-radius: 6px; /* Solo cambiar la forma a cuadrada */
      }
    }
  }
  
  .conversation-area {
    padding-top: var(--iphone-14-header-height);
    padding-bottom: var(--iphone-14-footer-height);
    
    .chat-container {
      padding: var(--iphone-14-padding);
    }
  }
  
  .mobile-controls-footer {
    height: var(--iphone-14-footer-height);
    padding: var(--iphone-14-padding);
  }
}

// iPhone XE y pantallas ultra-pequeñas (375px y menor)
@media screen and (max-width: 375px) {
  :root {
    --mobile-padding: var(--ultra-small-padding);
    --mobile-gap: var(--ultra-small-gap);
    --mobile-touch-target: var(--ultra-small-touch-target);
    --mobile-header-height: var(--ultra-small-header-height);
    --mobile-footer-height: var(--ultra-small-footer-height);
    --mobile-font-base: var(--ultra-small-font);
  }
  
  .mobile-header {
    height: var(--ultra-small-header-height);
    
    .header-content {
      padding: 0 var(--ultra-small-padding);
      
      .app-title {
        font-size: 13px;
        font-weight: 500;
      }
      
      .youtube-btn,
      .chatbot-vox-btn,
      .study-btn,
      .calendar-btn,
      .ponitivox-btn,
      .user-profile-btn,
      .control-panel-btn {
        width: 32px;
        height: 32px;
        font-size: 12px;
      }
      
      .chatbot-vox-btn {
        border-radius: 5px; /* Solo cambiar la forma a cuadrada */
      }
    }
  }
  
  .conversation-area {
    padding-top: var(--ultra-small-header-height);
    padding-bottom: var(--ultra-small-footer-height);
    
    .chat-container {
      padding: var(--ultra-small-padding);
      gap: var(--ultra-small-gap);
    }
  }
  
  .mobile-controls-footer {
    height: var(--ultra-small-footer-height);
    padding: var(--ultra-small-padding);
  }
}

// Samsung S6 Edge y pantallas ultra-pequeñas
@media screen and (max-width: 360px) {
  :root {
    --mobile-padding: var(--ultra-small-padding);
    --mobile-gap: var(--ultra-small-gap);
    --mobile-touch-target: var(--ultra-small-touch-target);
    --mobile-font-base: var(--ultra-small-font);
  }
  
  .mobile-header {
    height: 50px;
    
    .header-content {
      padding: 0 var(--ultra-small-padding);
      
      .app-title {
        font-size: 15px;
      }
      
      .youtube-btn,
      .chatbot-vox-btn,
      .study-btn,
      .calendar-btn,
      .ponitivox-btn,
      .user-profile-btn,
      .control-panel-btn {
        width: 40px;
        height: 40px;
        font-size: 16px;
      }
      
      .chatbot-vox-btn {
        border-radius: 7px; /* Solo cambiar la forma a cuadrada */
      }
    }
  }
  
  .conversation-area {
    padding-top: 50px;
    padding-bottom: 100px;
  }
  
  .mobile-controls-footer {
    height: 100px;
    padding: var(--ultra-small-padding);
  }
}

// Landscape para móviles pequeños
@media screen and (max-height: 500px) and (orientation: landscape) {
  .mobile-header {
    height: 40px;
  }
  
  .mobile-controls-footer {
    height: 80px;
  }
  
  .conversation-area {
    padding-top: 40px;
    padding-bottom: 80px;
  }
}

// Para tablets y desktop, usar layout horizontal
@media screen and (min-width: 768px) {
  .mobile-chat-layout {
    flex-direction: row;
  }
  
  .mobile-header {
    position: relative;
    width: 300px;
    height: 100%;
    border-right: 1px solid var(--mobile-border);
    border-bottom: none;
    
    .header-content {
      flex-direction: column;
      align-items: flex-start; // Align items to the start for vertical layout
      padding: var(--mobile-padding); // Use existing variable for consistency
      gap: calc(var(--mobile-gap) * 1.5); // Increase gap for better vertical spacing

      .app-title {
        font-size: 1.6rem; // Make title more prominent in sidebar
        font-weight: 700; // Bolder
        padding: 15px 5px; // Add some vertical padding, keep horizontal minimal
        text-align: left; // Ensure text is left-aligned
        width: 100%; // Allow it to take full width for alignment purposes
        color: var(--mobile-text); // Ensure high contrast
        display: flex;
        align-items: center;
        gap: 12px;
        
        .voxia-logo {
          width: 40px;
          height: 40px;
          object-fit: contain;
          filter: brightness(1.1);
          flex-shrink: 0;
        }
        // Optional: Add a bottom border for visual separation from actions
        // border-bottom: 1px solid var(--mobile-border);
        // margin-bottom: var(--mobile-gap);
      }

      .header-actions {
        display: flex;
        flex-direction: column; // Stack action buttons vertically
        width: 100%; // Container for actions takes full width
        gap: var(--mobile-gap); // Standard gap between buttons

        // Styling for buttons in sidebar mode
        // Note: .settings-btn is mobile-only and should not appear here.
        .youtube-btn,
        .chatbot-vox-btn,
        .study-btn,
        .calendar-btn,
        .ponitivox-btn,
        .user-profile-btn,
        .control-panel-btn {
          width: 100%; // Buttons take full width of the actions container
          height: auto; // Adjust height to content or set a fixed suitable height
          padding: 12px 15px; // Comfortable padding for full-width buttons
          justify-content: flex-start; // Align icon/text to the left
          font-size: 1.5rem; // Icon size (original was 20px, this is ~24px)
                              // If buttons were to have text, adjust font-size for text instead.
          background: var(--mobile-surface); // Maintain original background
          color: var(--mobile-text); // Maintain original icon color
          border-radius: var(--mobile-border-radius); // Consistent border radius

          // Adjust hover/active states if needed, e.g., less aggressive scaling
          &:hover {
            background: var(--mobile-accent); // Keep consistent hover
          }
          &:active {
            transform: scale(0.98); // Slightly more subtle active state for sidebar
          }
        }
      }
    }
  }
  
  .conversation-area {
    flex: 1;
    padding: 0;
  }
  
  .mobile-controls-footer {
    position: relative;
    width: 100%;
    height: auto;
    border-top: none;
    border-left: 1px solid var(--mobile-border);
  }
}
