// ========== RESPONSIVE DESIGN - MÓVILES ==========

@media (max-width: 768px) {
  .interactive-calendar {
    border-radius: 0;
    
    .calendar-header {
      padding: 16px 20px;
      flex-wrap: wrap;
      gap: 12px;
      
      .calendar-navigation {
        order: 1;
        width: 100%;
        justify-content: center;
        
        .nav-btn {
          width: 40px;
          height: 40px;
          font-size: 18px;
        }
        
        .current-date h3 {
          font-size: 20px;
        }
      }
      
      .calendar-controls {
        order: 2;
        width: 100%;
        justify-content: center;
        
        .view-btn, .today-btn {
          padding: 8px 12px;
          font-size: 12px;
        }
      }
    }
  }
  
  .calendar-month-view {
    .calendar-month-grid {
      .calendar-day {
        min-height: 80px;
        padding: 6px;
        
        .day-number {
          font-size: 14px;
          
          &.today .day-number {
            width: 28px;
            height: 28px;
            font-size: 12px;
          }
        }
        
        .day-events {
          .calendar-event-item {
            padding: 4px 6px;
            font-size: 10px;
            min-height: 16px;
            
            .event-time {
              font-size: 8px;
            }
          }
        }
      }
    }
  }
  
  .advanced-event-form {
    width: 100vw;
    height: 100vh;
    max-width: none;
    max-height: none;
    border-radius: 0;
    top: 0;
    left: 0;
    transform: none;
    
    .form-header {
      padding: 16px 20px;
      
      h2 {
        font-size: 18px;
      }
      
      .close-btn {
        width: 36px;
        height: 36px;
        font-size: 20px;
      }
    }
    
    .event-form {
      padding: 20px;
      max-height: calc(100vh - 80px);
      
      .form-section {
        margin-bottom: 24px;
        
        h3 {
          font-size: 16px;
        }
        
        .form-group {
          margin-bottom: 16px;
          
          label {
            font-size: 14px;
          }
          
          input, textarea, select {
            padding: 12px 16px;
            font-size: 16px; /* Para evitar zoom en iOS */
          }
        }
        
        .datetime-group {
          grid-template-columns: 1fr;
          gap: 16px;
        }
      }
      
      .form-actions {
        padding-top: 20px;
        margin-top: 24px;
        
        .cancel-btn, .submit-btn {
          padding: 14px 24px;
          font-size: 14px;
        }
      }
    }
  }
}

@media (max-width: 480px) {
  .calendar-month-view {
    .calendar-weekdays {
      .calendar-weekday {
        padding: 12px 4px;
        font-size: 11px;
      }
    }
    
    .calendar-month-grid {
      .calendar-day {
        min-height: 70px;
        padding: 4px;
        
        .day-number {
          font-size: 12px;
          margin-bottom: 4px;
        }
        
        .day-events {
          gap: 2px;
          
          .calendar-event-item {
            padding: 2px 4px;
            font-size: 9px;
            min-height: 14px;
            border-radius: 4px;
            
            .event-time {
              font-size: 7px;
            }
            
            .event-title {
              line-height: 1.2;
            }
          }
          
          .more-events {
            font-size: 8px;
            padding: 2px;
          }
        }
      }
    }
  }
}