/**
 * Estilos para el componente de visualización de video mejorado
 * Optimizado para PC y móvil con mejor experiencia visual
 */

.video-display {
  position: relative;
  width: 100%;
  height: 100%;
  background: #000;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.3);
  transition: all 0.3s ease;

  &.webcam {
    /* Estilos específicos para cámara */
    border: 2px solid rgba(74, 144, 226, 0.5);
    
    &:hover {
      border-color: rgba(74, 144, 226, 0.8);
      box-shadow: 0 6px 25px rgba(74, 144, 226, 0.2);
    }
  }

  &.screen {
    /* Estilos específicos para pantalla */
    border: 2px solid rgba(255, 193, 7, 0.5);
    
    &:hover {
      border-color: rgba(255, 193, 7, 0.8);
      box-shadow: 0 6px 25px rgba(255, 193, 7, 0.2);
    }
  }

  &.fullscreen {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    z-index: 10000;
    border-radius: 0;
    border: none;
  }

  /* Elemento de video principal */
  .video-element {
    width: 100%;
    height: 100%;
    object-fit: cover;
    background: #000;
    
    /* Optimizaciones de rendimiento */
    transform: translateZ(0);
    backface-visibility: hidden;
    -webkit-backface-visibility: hidden;
  }

  /* Indicador de tipo en la esquina superior */
  .video-type-indicator {
    position: absolute;
    top: 8px;
    left: 8px;
    background: rgba(0, 0, 0, 0.8);
    color: white;
    padding: 4px 8px;
    border-radius: 6px;
    font-size: 12px;
    display: flex;
    align-items: center;
    gap: 4px;
    z-index: 2;
    backdrop-filter: blur(10px);

    .type-icon {
      font-size: 14px;
    }

    .type-label {
      font-weight: 500;
    }

    .resolution {
      opacity: 0.7;
      font-size: 10px;
      margin-left: 4px;
    }
  }

  /* Controles de video */
  .video-controls {
    position: absolute;
    bottom: 8px;
    right: 8px;
    display: flex;
    gap: 6px;
    opacity: 0;
    transition: opacity 0.3s ease;
    z-index: 2;

    .control-button {
      background: rgba(0, 0, 0, 0.8);
      border: none;
      color: white;
      width: 36px;
      height: 36px;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      cursor: pointer;
      font-size: 16px;
      transition: all 0.2s ease;
      backdrop-filter: blur(10px);

      &:hover {
        background: rgba(0, 0, 0, 0.9);
        transform: scale(1.1);
      }

      &:active {
        transform: scale(0.95);
      }
    }
  }

  /* Mostrar controles al hover */
  &:hover .video-controls {
    opacity: 1;
  }

  /* Estado de carga */
  .video-loading {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background: rgba(0, 0, 0, 0.8);
    color: white;
    gap: 12px;
    z-index: 3;

    .loading-spinner {
      width: 40px;
      height: 40px;
      border: 3px solid rgba(255, 255, 255, 0.3);
      border-top: 3px solid #ffffff;
      border-radius: 50%;
      animation: spin 1s linear infinite;
    }

    span {
      font-size: 14px;
      opacity: 0.9;
    }
  }

  /* Estado de error */
  .video-error {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background: rgba(45, 25, 25, 0.95);
    color: white;
    gap: 8px;
    z-index: 3;

    .error-icon {
      font-size: 32px;
    }

    .error-message {
      font-size: 14px;
      text-align: center;
      opacity: 0.9;
    }
  }

  /* Overlay de pantalla completa */
  .fullscreen-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
    z-index: 4;

    .fullscreen-info {
      position: absolute;
      top: 20px;
      left: 50%;
      transform: translateX(-50%);
      background: rgba(0, 0, 0, 0.8);
      color: white;
      padding: 12px 20px;
      border-radius: 8px;
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 4px;
      backdrop-filter: blur(10px);
      animation: fadeInOut 3s ease-in-out;

      .fullscreen-hint {
        font-size: 12px;
        opacity: 0.7;
      }
    }
  }
}

/* Placeholder cuando no hay video */
.video-display-placeholder {
  width: 100%;
  height: 100%;
  min-height: 200px;
  background: rgba(25, 25, 28, 0.8);
  border: 2px dashed rgba(255, 255, 255, 0.3);
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;

  &:hover {
    border-color: rgba(255, 255, 255, 0.5);
    background: rgba(25, 25, 28, 0.9);
  }

  .placeholder-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 8px;
    color: rgba(255, 255, 255, 0.6);

    .placeholder-icon {
      font-size: 48px;
      opacity: 0.5;
    }

    .placeholder-text {
      font-size: 14px;
      font-weight: 500;
    }
  }
}

/* Responsive design */
@media (max-width: 768px) {
  .video-display {
    border-radius: 8px;

    .video-type-indicator {
      top: 4px;
      left: 4px;
      padding: 2px 6px;
      font-size: 11px;

      .type-icon {
        font-size: 12px;
      }

      .resolution {
        display: none; /* Ocultar resolución en móvil */
      }
    }

    .video-controls {
      bottom: 4px;
      right: 4px;
      gap: 4px;

      .control-button {
        width: 32px;
        height: 32px;
        font-size: 14px;
      }
    }

    /* Mostrar controles siempre en móvil */
    .video-controls {
      opacity: 1;
    }

    .fullscreen-overlay .fullscreen-info {
      top: 10px;
      padding: 8px 16px;
      font-size: 14px;

      .fullscreen-hint {
        font-size: 11px;
      }
    }
  }

  .video-display-placeholder {
    min-height: 150px;

    .placeholder-content {
      .placeholder-icon {
        font-size: 36px;
      }

      .placeholder-text {
        font-size: 12px;
      }
    }
  }
}

/* Pantallas muy pequeñas */
@media (max-width: 480px) {
  .video-display {
    .video-type-indicator {
      font-size: 10px;
      padding: 1px 4px;

      .type-icon {
        font-size: 11px;
      }
    }

    .video-controls .control-button {
      width: 28px;
      height: 28px;
      font-size: 12px;
    }
  }
}

/* Optimizaciones para PC */
@media (min-width: 1024px) {
  .video-display {
    &:hover {
      transform: scale(1.02);
    }

    .video-type-indicator {
      .resolution {
        display: inline; /* Mostrar resolución en PC */
      }
    }
  }
}

/* Animaciones */
@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

@keyframes fadeInOut {
  0%, 100% { opacity: 0; }
  20%, 80% { opacity: 1; }
}

/* Modo Picture in Picture */
.video-display.pip-active {
  .video-element {
    /* Estilos cuando está en PiP */
    border: 2px solid rgba(255, 193, 7, 0.8);
  }
}

/* Mejoras de accesibilidad */
.video-display {
  .control-button:focus {
    outline: 2px solid #4285f4;
    outline-offset: 2px;
  }

  .video-element:focus {
    outline: 2px solid #4285f4;
    outline-offset: -2px;
  }
}

/* Modo alto contraste */
@media (prefers-contrast: high) {
  .video-display {
    border-width: 3px;
    
    .video-type-indicator,
    .video-controls .control-button {
      background: rgba(0, 0, 0, 0.95);
      border: 1px solid rgba(255, 255, 255, 0.5);
    }
  }
}

/* Reducir movimiento para usuarios que lo prefieren */
@media (prefers-reduced-motion: reduce) {
  .video-display,
  .video-controls,
  .control-button {
    transition: none;
    animation: none;
  }
  
  .video-display:hover {
    transform: none;
  }
}