 * 100% libre de copyright - Licencia MIT
 */

.youtube-panel {
  position: fixed;
  width: 340px; // Ancho aumentado para mejor visualización
  max-height: calc(100vh - 160px); // Altura ajustada para que el video sea visible
  background: #111827; // bg-gray-900 dark
  border: 1px solid #374151; // border-gray-600
  border-radius: 16px; // Más redondeado
  box-shadow: 0 12px 48px rgba(0, 0, 0, 0.4); // Sombra más dramática
  z-index: 200;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  transition: all 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  font-family: 'Roboto', sans-serif; // Tipografía moderna
  color: #ffffff;

  // En móviles, usar posición CSS fija (igual que StudyPanel)
  @media (max-width: 768px) {
    top: 20px !important;
    left: 10px !important;
    right: 10px !important;
    bottom: 20px !important;
    width: calc(100vw - 20px) !important;
    max-width: 400px !important;
    transform: none !important;
    transition: all 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  }

  // Para screen sharing mode
  &.mini-mode {
    top: 70px !important;
    right: 10px !important;
    transform: none !important;
  }

  &.minimized {
    height: 60px;
    max-height: 60px;

    .youtube-panel-content {
      display: none;
    }
  }

  // Clases específicas para drag
  &.dragging {
    z-index: 9999;
    box-shadow: 0 25px 80px rgba(0, 0, 0, 0.4);
  }

  &.draggable {
    .drag-handle {
      opacity: 1;
    }
  }
}

/* RESPONSIVE: Media queries para diferentes tamaños de pantalla */

/* Pantallas grandes (Desktop) */
@media screen and (min-width: 1200px) {
  .youtube-panel {
    width: 380px; // Más ancho en pantallas grandes
    
    .video-container {
      aspect-ratio: 16 / 9; // Proporción perfecta
    }
  }
}

/* Pantallas medianas (Tablet landscape) */
@media screen and (min-width: 768px) and (max-width: 1199px) {
  .youtube-panel {
    width: 320px; // Ancho medio
    
    .video-container {
      aspect-ratio: 16 / 9;
    }
  }
}

/* Pantallas pequeñas (Tablet portrait y móvil landscape) */
@media screen and (min-width: 481px) and (max-width: 767px) {
  .youtube-panel {
    width: 300px; // Más compacto
    
    .video-container {
      aspect-ratio: 16 / 9;
    }
  }
}

/* Móviles (Portrait) */
@media screen and (max-width: 480px) {
  .youtube-panel {
    &:not(.mobile) {
      width: calc(100vw - 20px);
      max-width: 320px;
      left: 10px !important;
      right: 10px !important;
      top: 20px !important;
      bottom: 20px !important;
      max-height: calc(100vh - 40px);
    }
    
    .video-container {
      aspect-ratio: 16 / 9;
      width: 100%;
      min-height: 180px;
    }
    
    .youtube-panel-content {
      padding: 12px;
      gap: 12px;
    }
    
    .youtube-panel-header {
      padding: 12px 16px 8px;
      
      .panel-title h3 {
        font-size: 14px;
      }
    }
    
    .search-form .search-input-container {
      flex-direction: column;
      gap: 8px;
      
      .search-input {
        padding: 12px;
        font-size: 16px; /* Evita zoom en iOS */
      }
      
      .search-btn {
        padding: 12px;
        width: 100%;
      }
    }
  }
}

/* Optimizaciones específicas para tablets y móviles */
@media screen and (max-width: 768px) {
  .youtube-panel {
    &:not(.mobile) {
      width: 300px;
      max-height: calc(100vh - 120px);
    }
    
    &.right {
      right: 10px;
      bottom: 80px;
    }
    
    &.left {
      left: 10px;
      bottom: 80px;
    }
  }
  
  .video-container {
    aspect-ratio: 16 / 9;
    width: 100%;
    min-height: 160px;
    
    .youtube-iframe {
      width: 100%;
      height: 100%;
      border-radius: 12px;
    }
    
    .placeholder {
      width: 100%;
      height: 100%;
      border-radius: 12px;
      display: flex;
      align-items: center;
      justify-content: center;
      background: #1f2937;
      
      .placeholder-content {
        text-align: center;
        color: #9ca3af;
        
        .material-icons {
          font-size: 48px;
          color: #a855f7;
          margin-bottom: 16px;
        }
        
        p {
          margin: 8px 0;
          font-size: 14px;
        }
        
        small {
          font-size: 12px;
          opacity: 0.7;
        }
      }
    }
  }
  
  .youtube-panel-header {
    padding: 16px 20px 12px;
    
    .panel-title h3 {
      font-size: 15px;
    }
    
    .panel-controls button {
      padding: 6px;
    }
  }
  
  .youtube-panel-content {
    padding: 16px 20px;
    gap: 16px;
  }
}

.youtube-panel-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px 24px 16px;
  background: #1f2937; // bg-gray-800
  border-bottom: 1px solid #374151; // border-gray-600
  min-height: 60px;
  font-family: 'Roboto', sans-serif;

  .panel-title {
    display: flex;
    align-items: center;
    gap: 12px;
    
    h3 {
      margin: 0;
      font-size: 16px;
      font-weight: 600;
      color: #ffffff;
      
      .material-icons {
        color: #a855f7; // text-purple-400
      }
    }

    .youtube-icon {
      font-size: 20px;
      color: #a855f7; // text-purple-400
    }

    // Indicador de arrastre (solo en desktop)
    .drag-indicator {
      margin-left: 8px;
      color: #6b7280;
      font-size: 12px;
      font-weight: 400;
      letter-spacing: -1px;
      opacity: 0.7;
      transition: opacity 0.2s ease;
      user-select: none;
    }

    // Nuevo drag handle button
    .drag-handle {
      background: transparent;
      border: none;
      color: #6b7280;
      cursor: grab;
      padding: 4px;
      border-radius: 4px;
      transition: all 0.2s ease;
      margin-left: 8px;
      opacity: 0.7;

      &:hover {
        color: #a855f7;
        background: rgba(168, 85, 247, 0.1);
        opacity: 1;
      }

      &:active {
        cursor: grabbing;
      }

      svg {
        width: 16px;
        height: 16px;
      }
    }
  }

  .panel-controls {
    display: flex;
    align-items: center;
    gap: 8px;

    button {
      background: transparent;
      border: none;
      color: #9ca3af; // text-gray-400
      cursor: pointer;
      padding: 8px;
      border-radius: 8px;
      display: flex;
      align-items: center;
      justify-content: center;
      transition: all 0.2s ease;
      font-family: 'Roboto', sans-serif;

      &:hover {
        background: #374151; // hover:bg-gray-700
        color: #ffffff;
        transform: scale(1.05);
      }

      &.minimize-btn {
        font-size: 14px;
      }

      &.close-btn {
        font-size: 18px;
      }

      &.autoplay-btn {
        font-size: 16px;
        position: relative;
        
        &.active {
          color: #a855f7;
          background: rgba(168, 85, 247, 0.1);
        }
        
        &:not(.active) {
          color: #9ca3af;
          
          &:hover {
            color: #ffffff;
            background: #374151;
          }
        }
      }
    }
  }
}

/* Estilos para el arrastre del panel (solo desktop) */
.youtube-panel:not(.mobile) {
  &.dragging {
    box-shadow: 
      0 25px 50px -12px rgba(0, 0, 0, 0.4),
      0 10px 20px -5px rgba(168, 85, 247, 0.3),
      0 0 0 1px rgba(255, 255, 255, 0.1);
    transform: scale(1.02);
    z-index: 9999 !important;
    
    .youtube-panel-header {
      background: #2d3748;
      
      .drag-indicator {
        opacity: 1;
        color: #a855f7;
      }
    }
  }
  
  .youtube-panel-header:hover {
    .drag-indicator {
      opacity: 1;
    }
  }
}

.youtube-panel-content {
  display: flex;
  flex-direction: column;
  flex: 1;
  overflow-y: auto; /* Barra de desplazamiento hacia abajo */
  overflow-x: hidden;
  padding: 16px; // Padding reducido para más espacio
  gap: 16px; // Gap reducido para optimizar espacio
  background: #111827; // bg-gray-900
  font-family: 'Roboto', sans-serif;
  min-height: 0; // Importante para que el scroll funcione correctamente
  
  /* Estilos personalizados para la barra de desplazamiento */
  &::-webkit-scrollbar {
    width: 8px; // Barra más visible
  }
  
  &::-webkit-scrollbar-track {
    background: #1f2937; // bg-gray-800
    border-radius: 4px;
  }
  
  &::-webkit-scrollbar-thumb {
    background: #a855f7; // Morado para que coincida con la paleta
    border-radius: 4px;
    
    &:hover {
      background: #9333ea; // Morado más oscuro en hover
    }
  }
  
  /* Para Firefox */
  scrollbar-width: thin;
  scrollbar-color: #a855f7 #1f2937;
}

.search-form {
  .search-input-container {
    display: flex;
    gap: 12px;
    align-items: center;

    .search-input {
      flex: 1;
      padding: 16px;
      border: 1px solid #374151; // border-gray-600
      border-radius: 12px;
      background: #1f2937; // bg-gray-800
      color: #ffffff;
      font-size: 14px;
      font-family: 'Roboto', sans-serif;
      transition: all 0.2s ease;

      &:focus {
        outline: none;
        border-color: #a855f7; // border-purple-400
        box-shadow: 0 0 0 3px rgba(168, 85, 247, 0.2);
      }

      &:disabled {
        opacity: 0.6;
        cursor: not-allowed;
      }

      &::placeholder {
        color: #9ca3af; // text-gray-400
      }
    }

    .search-btn {
      padding: 16px;
      background: #a855f7; // bg-purple-400
      color: white;
      border: none;
      border-radius: 12px;
      cursor: pointer;
      display: flex;
      align-items: center;
      justify-content: center;
      min-width: 48px;
      transition: all 0.2s ease;
      font-family: 'Roboto', sans-serif;

      &:hover:not(:disabled) {
        background: #9333ea; // hover:bg-purple-500
        transform: scale(1.05);
      }

      &:disabled {
        opacity: 0.6;
        cursor: not-allowed;
      }

      .loading-spinner {
        animation: spin 1s linear infinite;
      }
    }
  }
}

// Estilos para el botón de música aleatoria
.random-music-section {
  padding: 12px 20px;
  border-bottom: 1px solid #374151;

  .random-music-btn {
    width: 100%;
    padding: 14px 20px;
    background: linear-gradient(45deg, #f59e0b, #f97316); // gradient amarillo-naranja
    color: white;
    border: none;
    border-radius: 12px;
    cursor: pointer;
    font-size: 14px;
    font-weight: 600;
    font-family: 'Roboto', sans-serif;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 8px;
    transition: all 0.3s ease;
    text-transform: uppercase;
    letter-spacing: 0.5px;

    &:hover:not(:disabled) {
      background: linear-gradient(45deg, #d97706, #ea580c);
      transform: translateY(-2px);
      box-shadow: 0 6px 20px rgba(249, 115, 22, 0.4);
    }

    &:active:not(:disabled) {
      transform: translateY(0);
    }

    &:disabled {
      opacity: 0.6;
      cursor: not-allowed;
      transform: none;
    }
  }
}

.playback-controls {
  display: flex;
  flex-direction: column;
  gap: 16px;
  padding: 20px;
  background: #1f2937; // bg-gray-800
  border-radius: 16px;
  border: 1px solid #374151; // border-gray-600
  font-family: 'Roboto', sans-serif;

  .media-controls {
    display: flex;
    align-items: center;
    gap: 16px;
  }

  .play-pause-btn {
    background: #a855f7; // bg-purple-400
    color: white;
    border: none;
    border-radius: 50%;
    width: 44px;
    height: 44px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: all 0.2s ease;
    font-family: 'Roboto', sans-serif;

    &:hover {
      background: #9333ea; // hover:bg-purple-500
      transform: scale(1.05);
    }
  }

  .volume-controls {
    display: flex;
    align-items: center;
    gap: 12px;
    flex: 1;

    .mute-btn {
      background: transparent;
      border: none;
      font-size: 20px;
      color: #9ca3af; // text-gray-400
      cursor: pointer;
      padding: 8px;
      border-radius: 8px;
      transition: all 0.2s ease;
      font-family: 'Roboto', sans-serif;

      &:hover {
        background: #374151; // hover:bg-gray-700
        color: #ffffff;
      }
    }

    .volume-slider {
      flex: 1;
      max-width: 100px;
      height: 6px;
      background: #374151; // bg-gray-600
      border-radius: 8px;
      outline: none;
      cursor: pointer;
      transition: all 0.2s ease;
      
      &::-webkit-slider-thumb {
        appearance: none;
        width: 18px;
        height: 18px;
        background: #a855f7; // bg-purple-400
        border-radius: 50%;
        cursor: pointer;
        transition: all 0.2s ease;

        &:hover {
          background: #9333ea; // hover:bg-purple-500
          transform: scale(1.1);
        }
      }

      &::-moz-range-thumb {
        width: 18px;
        height: 18px;
        background: #a855f7; // bg-purple-400
        border-radius: 50%;
        border: none;
        cursor: pointer;
        transition: all 0.2s ease;

        &:hover {
          background: #9333ea; // hover:bg-purple-500
          transform: scale(1.1);
        }
      }
    }

    .volume-label {
      font-size: 12px;
      color: #9ca3af; // text-gray-400
      min-width: 30px;
      text-align: right;
      font-family: 'Roboto', sans-serif;
    }
  }

  .current-track {
    font-size: 14px;
    color: #d1d5db; // text-gray-300
    text-align: center;
    font-family: 'Roboto', sans-serif;
  }

  &.mini {
    padding: 12px 16px;
    background: #1f2937; // bg-gray-800
    
    .media-controls {
      gap: 12px;
    }

    .play-pause-btn {
      width: 32px;
      height: 32px;
    }

    .volume-controls .mute-btn {
      font-size: 16px;
    }
  }
}

.video-container {
  flex-shrink: 0; // No se comprime
  background: #000000;
  border-radius: 16px;
  overflow: hidden;
  position: relative;
  border: 1px solid #374151; // border-gray-600
  
  // FIXED: Altura fija para asegurar visibilidad completa
  width: 100%;
  height: 180px; // Altura fija para que siempre sea visible
  min-height: 180px; // Altura mínima garantizada
  
  // Container flex para centrado perfecto
  display: flex;
  align-items: center;
  justify-content: center;

  .youtube-iframe {
    width: 100%;
    height: 100%;
    border-radius: 16px;
    border: none;
    
    // RESPONSIVE: Se ajusta automáticamente manteniendo proporción
    object-fit: contain;
    display: block;
  }

  .placeholder {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    background: #1f2937; // bg-gray-800

    .placeholder-content {
      text-align: center;
      color: #9ca3af; // text-gray-400
      font-family: 'Roboto', sans-serif;

      .placeholder-icon {
        font-size: 48px;
        margin-bottom: 16px;
        display: block;
        color: #a855f7; // text-purple-400
      }

      p {
        margin: 0 0 8px 0;
        font-size: 16px;
        color: #d1d5db; // text-gray-300
      }

      small {
        font-size: 12px;
        color: #6b7280; // text-gray-500
        font-family: 'Roboto', sans-serif;
      }
    }
  }
}

/* Animaciones */
@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

