/* Auto-reposicionamiento cuando hay otros paneles activos */
.youtube-panel.repositioned {
  &.right {
    right: 380px; /* Espacio para otros paneles */
  }

  &.left {
    left: 380px; /* Espacio para otros paneles */
  }
}

/* Estados de transición suaves */
.youtube-panel {
  &.entering {
    transform: translateX(100%);
    opacity: 0;
  }

  &.entered {
    transform: translateX(0);
    opacity: 1;
  }

  &.exiting {
    transform: translateX(100%);
    opacity: 0;
  }
}

/* Mejoras de accesibilidad */
.youtube-panel {
  button:focus {
    outline: 2px solid var(--Primary-50);
    outline-offset: 2px;
  }

  input:focus {
    outline: 2px solid var(--Primary-50);
    outline-offset: 2px;
  }
}

/* Soporte para modo oscuro mejorado */
@media (prefers-color-scheme: dark) {
  .youtube-panel {
    box-shadow: 0 8px 32px rgba(0, 0, 0, 0.4);

    .video-container {
      .placeholder {
        .placeholder-content {
          color: var(--Neutral-40);

          p {
            color: var(--Neutral-50);
          }

          small {
            color: var(--Neutral-30);
          }
        }
      }
    }
  }
}

/* Estilos para información de reproducción automática */
.autoplay-info {
  background: rgba(31, 41, 55, 0.8);
  border: 1px solid #374151;
  border-radius: 12px;
  padding: 12px 16px;
  margin-top: 16px;
  font-family: 'Roboto', sans-serif;

  .autoplay-status {
    display: flex;
    align-items: center;
    gap: 8px;
    color: #a855f7;
    font-size: 14px;
    font-weight: 500;
    margin-bottom: 8px;

    .autoplay-indicator {
      font-size: 12px;
      animation: spin 2s linear infinite;
    }
  }

  .next-video-info {
    small {
      color: #d1d5db;
      font-size: 12px;
      line-height: 1.4;
      display: block;

      strong {
        color: #ffffff;
        font-weight: 600;
      }

      span {
        color: #9ca3af;
        font-style: italic;
      }
    }
  }

  .autoplay-loading {
    small {
      color: #a855f7;
      font-size: 12px;
      font-style: italic;
      display: flex;
      align-items: center;
      gap: 4px;
    }
  }

  .autoplay-controls {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 16px 0;
    border-top: 1px solid #374151; // border-gray-600
    margin-top: 12px;
    gap: 12px;

    .autoplay-info {
      display: flex;
      flex-direction: column;
      gap: 6px;
      flex: 1;

      .autoplay-status {
        font-size: 13px;
        font-weight: 600;
        color: #10b981; // text-green-500
        font-family: 'Roboto', sans-serif;
        
        &:contains('OFF') {
          color: #9ca3af; // text-gray-400
        }
      }

      .next-video {
        font-size: 11px;
        color: #9ca3af; // text-gray-400
        opacity: 0.9;
        font-family: 'Roboto', sans-serif;
        line-height: 1.3;
      }
    }

    .autoplay-buttons {
      display: flex;
      gap: 8px;
      align-items: center;

      .autoplay-btn {
        width: 40px;
        height: 40px;
        border-radius: 12px; // Mismo estilo que otros botones
        border: none;
        background: transparent;
        color: #9ca3af; // text-gray-400
        font-size: 16px;
        cursor: pointer;
        transition: all 0.2s ease;
        display: flex;
        align-items: center;
        justify-content: center;
        font-family: 'Roboto', sans-serif;
        border: 1px solid #374151; // border-gray-600

        &.enabled {
          background: #a855f7; // bg-purple-400 (paleta del proyecto)
          color: #ffffff;
          border-color: #a855f7;
          animation: pulse 2s ease-in-out infinite;
        }

        &.disabled {
          background: transparent;
          color: #9ca3af; // text-gray-400
          border-color: #374151; // border-gray-600
        }

        &:hover {
          background: #374151; // hover:bg-gray-700
          color: #ffffff;
          transform: scale(1.05);
          border-color: #4b5563; // border-gray-500
        }

        &.enabled:hover {
          background: #9333ea; // hover:bg-purple-500
          border-color: #9333ea;
        }
      }

      .test-autoplay-btn {
        padding: 8px 12px;
        border-radius: 8px;
        border: 1px solid #374151; // border-gray-600
        background: transparent;
        color: #9ca3af; // text-gray-400
        font-size: 11px;
        cursor: pointer;
        transition: all 0.2s ease;
        font-family: 'Roboto', sans-serif;
        font-weight: 500;

        &:hover:not(:disabled) {
          background: #a855f7; // bg-purple-400
          color: #ffffff;
          border-color: #a855f7;
          transform: scale(1.05);
        }

        &:disabled {
          opacity: 0.5;
          cursor: not-allowed;
          background: transparent;
          color: #6b7280; // text-gray-500
        }
      }
    }
  }
}

/* Información de reproducción automática */
.autoplay-info-display {
  display: flex;
  flex-direction: column;
  gap: 4px;

  .current-track {
    font-size: 12px;
    font-weight: 600;
    color: #10b981; // text-green-500
    display: flex;
    align-items: center;
    gap: 4px;
  }

  .next-track {
    font-size: 11px;
    color: #a855f7; // text-purple-400
    font-weight: 500;
    opacity: 0.9;
    line-height: 1.3;
    
    &.loading {
      color: #f59e0b; // text-amber-500
      animation: pulse 2s infinite;
    }
  }
}

/* Animación suave para el indicador de autoplay */
@keyframes pulse {
  0%, 100% { 
    opacity: 1;
    transform: scale(1);
  }
  50% { 
    opacity: 0.8;
    transform: scale(1.02);
  }
}

/* Estilos para funcionalidad de arrastrar (Drag) */
.youtube-panel {
  &.dragging {
    opacity: 0.9;
    box-shadow: 0 20px 60px rgba(0, 0, 0, 0.6);
    transform-origin: center;
    user-select: none;
    pointer-events: auto;
    z-index: 9999;
    
    .youtube-panel-header {
      cursor: grabbing !important;
      background: #1f2937; // Fondo ligeramente más oscuro durante drag
    }
  }

  .drag-indicator {
    color: #6b7280; // text-gray-400
    font-size: 16px;
    line-height: 1;
    user-select: none;
    transition: color 0.2s;
    margin-left: 8px;
    opacity: 0.6;
    
    &:hover {
      color: #9ca3af; // text-gray-300
      opacity: 1;
    }
  }

  .youtube-panel-header {
    cursor: grab;
    
    &:hover {
      .drag-indicator {
        opacity: 1;
        color: #9ca3af;
      }
    }
    
    &:active {
      cursor: grabbing;
    }
  }
  
  // En modo móvil, ocultar indicador de drag
  &.mobile {
    .drag-indicator {
      display: none;
    }
    
    .youtube-panel-header {
      cursor: default;
    }
  }
  
  // En mini-mode (screen sharing), también ocultar drag
  &.mini-mode {
    .drag-indicator {
      display: none;
    }
    
    .youtube-panel-header {
      cursor: default;
    }
  }
}

/* ============ CUADRO DE SIGUIENTE CANCIÓN ============ */
.next-video-alert {
  position: fixed;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 99999;
  animation: slideDown 0.5s ease-out, pulse 2s infinite;
  
  @media (max-width: 768px) {
    top: 10px;
    left: 10px;
    right: 10px;
    transform: none;
  }
}

.next-video-content {
  background: linear-gradient(135deg, 
    rgba(255, 69, 0, 0.95) 0%, 
    rgba(255, 140, 0, 0.95) 100%);
  border-radius: 15px;
  padding: 20px 25px;
  display: flex;
  align-items: center;
  gap: 15px;
  box-shadow: 
    0 15px 50px rgba(255, 69, 0, 0.4),
    0 8px 25px rgba(0, 0, 0, 0.3),
    0 0 30px rgba(255, 140, 0, 0.6);
  backdrop-filter: blur(15px);
  border: 2px solid rgba(255, 255, 255, 0.3);
  max-width: 400px;
  min-width: 300px;
  
  @media (max-width: 768px) {
    padding: 16px 20px;
    min-width: auto;
    max-width: none;
  }
}

.next-video-icon {
  font-size: 28px;
  animation: rotate 2s linear infinite;
  flex-shrink: 0;
}

.next-video-text {
  flex: 1;
  color: white;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.5);
}

.next-video-title {
  font-size: 14px;
  font-weight: 600;
  margin-bottom: 4px;
  opacity: 0.9;
}

.next-video-name {
  font-size: 16px;
  font-weight: 700;
  color: #ffffff;
  max-width: 250px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.next-video-animation {
  font-size: 24px;
  animation: bounce 1.5s infinite;
  flex-shrink: 0;
}

@keyframes slideDown {
  from {
    transform: translateX(-50%) translateY(-100px);
    opacity: 0;
  }
  to {
    transform: translateX(-50%) translateY(0);
    opacity: 1;
  }
}

@keyframes pulse {
  0%, 100% {
    transform: translateX(-50%) scale(1);
  }
  50% {
    transform: translateX(-50%) scale(1.02);
  }
}

@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-5px);
  }
  60% {
    transform: translateY(-3px);
  }
}
