
// Responsive variables
:root {
  --logger-padding-desktop: 25px;
  --logger-padding-tablet: 20px;
  --logger-padding-mobile: 12px;
  --logger-font-size-desktop: 14px;
  --logger-font-size-mobile: 12px;
  --logger-line-height-desktop: 1.4;
  --logger-line-height-mobile: 1.3;
  --logger-item-padding-desktop: 8px;
  --logger-item-padding-mobile: 12px;
}

.logger {
  color: var(--gray-300);
  width: 100%;
  max-width: 100%;
  display: block;
  // Enable touch scrolling on mobile
  -webkit-overflow-scrolling: touch;

  .logger-list {
    padding: 0 0px 0 var(--logger-padding-desktop);
    overflow-x: hidden;
    width: calc(100% - 45px);
    // Improve scrolling performance
    -webkit-overflow-scrolling: touch;
    overflow-y: auto;
  }

  .user h4 {
    color: var(--Green-500);
  }

  .model h4 {
    color: var(--Blue-500);
  }

  .rich-log {
    display: flex;
    justify-content: center;
    gap: 4px;

    pre {
      overflow-x: auto;
      // Better touch scrolling
      -webkit-overflow-scrolling: touch;
      // Better readability on mobile
      font-size: clamp(var(--logger-font-size-mobile), 2.5vw, var(--logger-font-size-desktop));
    }

    display: block;

    h4 {
      font-size: clamp(12px, 3vw, 14px);
      text-transform: uppercase;
      padding: var(--logger-item-padding-desktop) 0;
      margin: 0;
    }

    h5 {
      margin: 0;
      padding-bottom: var(--logger-item-padding-desktop);
      border-bottom: 1px solid var(--Neutral-20);
      font-size: clamp(11px, 2.5vw, 13px);
    }

    .part {
      background: var(--Neutral-5);
      padding: clamp(10px, 3vw, 14px);
      margin-bottom: 4px;
      color: var(--Neutral-90);
      border-radius: 8px;
      // Better touch interaction
      min-height: 44px;
      display: flex;
      align-items: center;
    }
  }

  .plain-log {
    &>* {
      padding-right: 4px;
    }
  }

  .inline-code:not(:last-child) {
    font-style: italic;

    &::after {
      content: ", ";
    }
  }
}

.logger li {
  display: block;
  padding: var(--logger-item-padding-desktop) 0;
  color: var(--Neutral-50, #707577);
  font-family: "Space Mono";
  font-size: clamp(var(--logger-font-size-mobile), 2.5vw, var(--logger-font-size-desktop));
  font-style: normal;
  font-weight: 400;
  line-height: var(--logger-line-height-desktop);
  // Better touch targets
  min-height: 44px;
  display: flex;
  align-items: center;
  gap: 8px;
  // Enable text selection on mobile
  user-select: text;
  -webkit-user-select: text;
}

.logger li .timestamp {
  width: clamp(60px, 15vw, 70px);
  flex-grow: 0;
  flex-shrink: 0;
  color: var(--Neutral-50);
  font-size: clamp(10px, 2vw, 12px);
}

.logger li .source {
  flex-shrink: 0;
  font-weight: bold;
}

.logger li.source-server,
.logger li.receive {
  color: var(--Blue-500);
}

.logger li.source-client,
.logger li.send:not(.source-server) {
  color: var(--Green-500);
}

.logger li .count {
  background-color: var(--Neutral-5);
  font-size: clamp(10px, 2vw, 12px);
  padding: 0.3em 0.5em;
  line-height: 1em;
  vertical-align: middle;
  border-radius: 8px;
  color: var(--Blue-500);
  // Better touch target
  min-width: 24px;
  min-height: 24px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

.logger li .message {
  flex-grow: 1;
  color: var(--Neutral-50);
  word-wrap: break-word;
  overflow-wrap: break-word;
  hyphens: auto;
}

// Responsive breakpoints
@media screen and (max-width: 1024px) {
  :root {
    --logger-padding-desktop: var(--logger-padding-tablet);
    --logger-font-size-desktop: 13px;
  }
  
  .logger .logger-list {
    padding-left: var(--logger-padding-tablet);
    width: calc(100% - 35px);
  }
}

@media screen and (max-width: 768px) {
  :root {
    --logger-padding-desktop: var(--logger-padding-mobile);
    --logger-font-size-desktop: var(--logger-font-size-mobile);
    --logger-line-height-desktop: var(--logger-line-height-mobile);
    --logger-item-padding-desktop: var(--logger-item-padding-mobile);
  }

  .logger {
    .logger-list {
      padding-left: var(--logger-padding-mobile);
      width: calc(100% - 20px);
    }

    .rich-log {
      gap: 2px;

      h4 {
        padding: var(--logger-item-padding-mobile) 0 6px 0;
      }

      h5 {
        padding-bottom: 6px;
      }

      .part {
        padding: 8px;
        margin-bottom: 2px;
        border-radius: 6px;
      }
    }
  }

  .logger li {
    flex-direction: column;
    align-items: flex-start;
    gap: 4px;
    padding: var(--logger-item-padding-mobile) 0;

    .timestamp {
      font-size: 10px;
      width: auto;
      align-self: flex-end;
    }

    .source {
      font-weight: bold;
      font-size: 11px;
    }

    .count {
      font-size: 10px;
      padding: 0.2em 0.4em;
      min-width: 20px;
      min-height: 20px;
    }

    .message {
      font-size: var(--logger-font-size-mobile);
      line-height: var(--logger-line-height-mobile);
    }
  }
}

// Touch-specific optimizations
@media (hover: none) and (pointer: coarse) {
  .logger {
    .rich-log .part {
      min-height: 48px;
      padding: 12px;
    }
  }

  .logger li {
    min-height: 48px;
    padding: 12px 0;
    
    .count {
      min-width: 32px;
      min-height: 32px;
      font-size: 12px;
    }
  }
}

// High contrast mode support
@media (prefers-contrast: high) {
  .logger {
    .rich-log .part {
      border: 1px solid var(--Neutral-30);
    }

    li .count {
      border: 1px solid var(--Blue-400);
    }
  }
}

// Reduced motion support
@media (prefers-reduced-motion: reduce) {
  .logger {
    -webkit-overflow-scrolling: auto;
    
    .logger-list {
      -webkit-overflow-scrolling: auto;
    }
  }
}
