.side-panel {
  background: #111827; /* bg-gray-900 */
  width: 40px;
  /* when closed */
  display: flex;
  flex-direction: column;
  height: 100vh;
  height: 100dvh; /* Altura real en móviles */
  transition: all 0.2s ease-in;
  font-family: 'Roboto', -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif;
  border-right: 1px solid #374151; /* border-gray-600 */
  color: #e5e7eb; /* text-gray-200 */
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.6;
  position: relative;
  z-index: 100;

  .hidden {
    display: none !important;
  }

  /* Estados de apertura responsivos */
  &.open {
    width: var(--sidebar-width);
    
    .top {
      h2 {
        left: 0%;
        display: block;
        opacity: 1;
      }
    }
  }

  /* Adaptaciones para tablets */
  @media screen and (max-width: 1024px) {
    &.open {
      width: 320px;
    }
  }

  /* Adaptaciones para móviles */
  @media screen and (max-width: 768px) {
    position: fixed;
    left: 0;
    top: 0;
    z-index: 1000;
    border-right: none;
    box-shadow: 2px 0 10px rgba(0, 0, 0, 0.3);
    
    &:not(.open) {
      transform: translateX(-100%);
      width: 0;
    }
    
    &.open {
      width: var(--sidebar-width-mobile);
      transform: translateX(0);
    }
  }

  /* Dispositivos muy pequeños */
  @media screen and (max-width: 480px) {
    font-size: 12px;
    
    &.open {
      width: 100vw;
    }
  }
  
  /* Samsung S6 Edge y ultra-pequeños */
  @media screen and (max-width: 360px) {
    font-size: 11px;
    
    &.open {
      width: 100vw;
    }
    
    .side-panel-container {
      padding: 8px;
    }
    
    .top {
      padding: 8px 12px;
      min-height: 40px;
      
      h2 {
        font-size: 14px;
        line-height: 1.1;
      }
    }
    
    .input-container {
      padding: 8px 12px;
      min-height: 60px;
      
      .input-content {
        min-height: 40px;
        border-radius: 8px;
        padding: 8px 12px;
      }
      
      .input-area {
        font-size: 13px;
        padding: 10px 12px;
      }
      
      .send-button {
        min-width: 40px;
        min-height: 40px;
        font-size: 14px;
      }
    }
  }

  /* Modo landscape en móviles */
  @media screen and (max-height: 500px) and (orientation: landscape) {
    &.open {
      width: 60vw;
      max-width: 400px;
    }
  }

  .top {
    display: flex;
    width: calc(100% - 45px);
    justify-content: space-between;
    align-items: center;
    padding: 12px 20px 12px 25px;
    border-bottom: 1px solid var(--Neutral-20);
    /* Mejoras responsivas */
    min-height: var(--min-touch-target);

    h2 {
      position: relative;
      color: var(--Neutral-90, #e1e2e3);
      font-family: "Google Sans";
      font-size: 21px;
      font-style: normal;
      font-weight: 500;
      line-height: 16px;
      /* 100% */

      opacity: 0;
      display: none;
      left: -100%;
      transition:
        opacity 0.2s ease-in,
        left 0.2s ease-in,
        display 0.2s ease-in;
      transition-behavior: allow-discrete;

      @starting-style {
        left: 0%;
        opacity: 1;
      }
    }
    
    /* Adaptaciones móviles para el header */
    @media screen and (max-width: 768px) {
      width: 100%;
      padding: 15px 20px;
      
      h2 {
        font-size: 18px;
        line-height: 1.2;
      }
    }
    
    @media screen and (max-width: 480px) {
      padding: 12px 15px;
      
      h2 {
        font-size: 16px;
      }
    }
  }

  .opener {
    height: 30px;
    transition: transform 0.2s ease-in;
    background: transparent;
    border: none;
    cursor: pointer;
    color: var(--Neutral-90);
    /* Mejorar área táctil */
    min-width: var(--min-touch-target);
    min-height: var(--min-touch-target);
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 4px;
    
    &:hover {
      background: var(--Neutral-20);
    }
    
    &:focus {
      outline: 2px solid var(--Blue-500);
      outline-offset: 2px;
    }
    
    /* Adaptaciones móviles */
    @media screen and (max-width: 768px) {
      min-width: 48px;
      min-height: 48px;
    }
  }

  &:not(.open) {
    .side-panel-container {
      opacity: 0;
      display: none;
      transition: all 0.2s ease-in allow-discrete;
      transition-delay: 0.1s;
    }

    .indicators .streaming-indicator {
      width: 30px;
      opacity: 0;
    }

    .opener {
      transform: translate(-50%, 0);
    }

    .input-container {
      opacity: 0;
      display: none;
      transition: all 0.2s ease-in allow-discrete;
    }
    
    /* En móviles, cuando está cerrado se oculta completamente */
    @media screen and (max-width: 768px) {
      transform: translateX(-100%);
    }
  }

  .indicators {
    display: flex;
    padding: 24px 25px;
    justify-content: flex-end;
    gap: 21px;
    /* Adaptaciones responsivas */
    flex-wrap: wrap;
    
    @media screen and (max-width: 768px) {
      padding: 15px 20px;
      gap: 15px;
      justify-content: center;
    }
    
    @media screen and (max-width: 480px) {
      padding: 12px 15px;
      gap: 10px;
      flex-direction: column;
    }

    .streaming-indicator {
      user-select: none;
      border-radius: 4px;
      border: 1px solid var(--Neutral-20, #2a2f31);
      background: var(--Neutral-10, #1c1f21);
      display: flex;
      width: 136px;
      height: 30px;
      padding-left: 4px;
      justify-content: center;
      align-items: center;
      gap: 6px;
      flex-shrink: 0;
      text-align: center;
      font-family: "Space Mono";
      font-size: 14px;
      font-style: normal;
      font-weight: 400;
      line-height: normal;
      transition: width 0.2s ease-in;
      /* Mejoras responsivas */
      min-height: var(--min-touch-target);

      &.connected {
        color: var(--Blue-500, #0d9c53);
      }
      
      /* Adaptaciones móviles */
      @media screen and (max-width: 768px) {
        width: 120px;
        font-size: 12px;
        min-height: 40px;
      }
      
      @media screen and (max-width: 480px) {
        width: 100%;
        height: 36px;
        font-size: 11px;
      }
    }
  }

  .side-panel-container {
    align-self: flex-end;
    width: 400px;
    flex-grow: 1;
    overflow-x: hidden;
    overflow-y: auto;
    /* Scroll mejorado para móviles */
    -webkit-overflow-scrolling: touch;
    /*scrollbar-gutter: stable both-edges;*/
    
    /* Adaptaciones responsivas */
    @media screen and (max-width: 1024px) {
      width: 320px;
    }
    
    @media screen and (max-width: 768px) {
      width: 100%;
      align-self: stretch;
    }
    
    /* Scrollbar personalizado más visible en móviles */
    &::-webkit-scrollbar {
      width: 8px;
      
      @media screen and (max-width: 768px) {
        width: 12px;
      }
    }
    
    &::-webkit-scrollbar-track {
      background: var(--Neutral-10);
    }
    
    &::-webkit-scrollbar-thumb {
      background: var(--Neutral-30);
      border-radius: 4px;
      
      &:hover {
        background: var(--Neutral-50);
      }
    }
  }

  .input-container {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 16px;
    background: #1f2937; /* bg-gray-800 */
    border-top: 1px solid #374151; /* border-gray-600 */
    
    @media screen and (min-width: 769px) {
      position: relative;
      left: auto;
      right: auto;
      bottom: auto;
    }

    .input-content {
      position: relative;
      background: #111827; /* bg-gray-900 */
      border: 1px solid #374151; /* border-gray-600 */
      border-radius: 8px; /* rounded-lg */
      padding: 12px 16px;
      display: flex;
      align-items: center;
      gap: 8px;
      transition: all 0.3s ease;
      
      &:focus-within {
        outline: 2px solid #3b82f6; /* focus:ring-blue-500 */
        outline-offset: 2px;
      }

      .input-area {
        flex: 1;
        background: transparent;
        color: #ffffff;
        border: none;
        outline: none;
        resize: none;
        font-size: 14px;
        font-family: 'Roboto', sans-serif;
        line-height: 1.5;
        min-height: 20px;
        max-height: 120px;
        
        &::placeholder {
          color: #9ca3af; /* placeholder-gray-500 */
        }
        
        @media screen and (max-width: 480px) {
          font-size: 16px; /* Evitar zoom en iOS */
        }
      }
      
      .input-actions {
        display: flex;
        align-items: center;
        gap: 8px;
        
        .expand-button {
          background: transparent;
          border: none;
          color: #9ca3af; /* text-gray-400 */
          cursor: pointer;
          padding: 4px;
          border-radius: 4px;
          transition: all 0.3s ease;
          display: flex;
          align-items: center;
          justify-content: center;
          
          .material-icons {
            font-size: 20px;
          }
          
          &:hover {
            color: #ffffff;
            background: #374151; /* hover:bg-gray-600 */
          }
        }
        
        .send-button {
          background: #3b82f6; /* bg-blue-500 */
          border: none;
          color: #ffffff;
          cursor: pointer;
          padding: 8px;
          border-radius: 50%; /* rounded-full */
          transition: all 0.3s ease;
          display: flex;
          align-items: center;
          justify-content: center;
          min-width: 40px;
          min-height: 40px;
          
          .material-icons {
            font-size: 20px;
          }
          
          &:hover {
            background: #2563eb; /* hover:bg-blue-600 */
            transform: translateY(-1px);
            box-shadow: 0 4px 8px rgba(59, 130, 246, 0.3);
          }
          
          &:active {
            transform: translateY(0);
          }
          
          &:focus {
            outline: 2px solid rgba(59, 130, 246, 0.5);
            outline-offset: 2px;
          }
        }
      }
    }
    
    &.disabled {
      opacity: 0.6;
      pointer-events: none;
    }
  }

  // Estilos para la configuración de voz - Moderno con Tailwind
  .voice-settings-section {
    border-bottom: 1px solid #374151; /* border-gray-600 */
    padding: 16px;
    
    @media screen and (max-width: 768px) {
      padding: 16px 20px;
    }
    
    @media screen and (max-width: 480px) {
      padding: 16px;
    }

    .voice-settings-toggle {
      width: 100%;
      background: #3b82f6; /* bg-blue-500 */
      color: #ffffff;
      border: none;
      border-radius: 8px; /* rounded-lg */
      padding: 12px 16px;
      font-size: 14px;
      font-weight: 700; /* font-bold */
      cursor: pointer;
      transition: all 0.3s ease;
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 8px;
      margin-bottom: 16px;
      /* Mejoras responsivas */
      min-height: var(--min-touch-target);
      
      @media screen and (max-width: 768px) {
        min-height: 48px;
        padding: 12px 16px;
        font-size: 14px;
        border-radius: 8px;
      }
      
      @media screen and (max-width: 480px) {
        min-height: 52px;
        padding: 12px 16px;
        font-size: 14px;
        border-radius: 8px;
      }

      // Material Icon para micrófono
      .material-icons {
        font-size: 20px;
      }

      &:hover {
        background: #2563eb; /* hover:bg-blue-600 */
        transform: translateY(-1px);
        box-shadow: 0 4px 8px rgba(59, 130, 246, 0.3);
      }

      &:active {
        transform: translateY(0);
      }
      
      &:focus {
        outline: 2px solid rgba(59, 130, 246, 0.5);
        outline-offset: 2px;
      }
    }

    .voice-settings-panel {
      margin-top: 15px;
      background: var(--Neutral-10);
      border-radius: 8px;
      padding: 15px;
      border: 1px solid var(--Neutral-30);
      
      @media screen and (max-width: 768px) {
        margin-top: 12px;
        padding: 18px;
        border-radius: 10px;
      }
      
      @media screen and (max-width: 480px) {
        margin-top: 10px;
        padding: 15px;
        border-radius: 8px;
      }

      // Ajustar el tamaño del componente para el panel lateral
      h3 {
        font-size: 1rem;
        margin-bottom: 15px;
        
        @media screen and (max-width: 768px) {
          font-size: 1.1rem;
          margin-bottom: 12px;
        }
        
        @media screen and (max-width: 480px) {
          font-size: 1rem;
          margin-bottom: 10px;
        }
      }

      .voice-control-group {
        margin-bottom: 12px;
        
        @media screen and (max-width: 768px) {
          margin-bottom: 15px;
        }

        label {
          font-size: 0.8rem;
          margin-bottom: 4px;
          display: block;
          
          @media screen and (max-width: 768px) {
            font-size: 0.9rem;
            margin-bottom: 6px;
          }
          
          @media screen and (max-width: 480px) {
            font-size: 0.85rem;
            margin-bottom: 5px;
          }
        }
      }

      .voice-select {
        font-size: 0.8rem;
        padding: 6px 10px;
        width: 100%;
        border-radius: 4px;
        border: 1px solid var(--Neutral-30);
        background: var(--Neutral-00);
        color: var(--Neutral-90);
        
        @media screen and (max-width: 768px) {
          font-size: 0.9rem;
          padding: 10px 12px;
          border-radius: 6px;
          min-height: 44px;
        }
        
        @media screen and (max-width: 480px) {
          font-size: 16px; /* Evitar zoom en iOS */
          padding: 12px 14px;
          min-height: 48px;
        }
      }

      .voice-test-buttons {
        margin-top: 15px;
        display: flex;
        gap: 8px;
        flex-wrap: wrap;
        
        @media screen and (max-width: 768px) {
          margin-top: 18px;
          gap: 10px;
        }
        
        @media screen and (max-width: 480px) {
          margin-top: 15px;
          gap: 8px;
          flex-direction: column;
        }

        .voice-button {
          font-size: 0.8rem;
          padding: 8px 12px;
          min-width: 100px;
          border: none;
          border-radius: 4px;
          cursor: pointer;
          transition: all 0.2s ease;
          
          @media screen and (max-width: 768px) {
            font-size: 0.9rem;
            padding: 10px 15px;
            min-width: 120px;
            min-height: 44px;
            border-radius: 6px;
          }
          
          @media screen and (max-width: 480px) {
            font-size: 0.95rem;
            padding: 12px 18px;
            min-width: 100%;
            min-height: 48px;
            border-radius: 8px;
          }
          
          &:focus {
            outline: 2px solid var(--Blue-500);
            outline-offset: 2px;
          }
        }
      }

      .voice-info {
        margin-top: 12px;
        padding: 10px;
        background: var(--Neutral-5);
        border-radius: 4px;
        border-left: 3px solid var(--Blue-500);
        
        @media screen and (max-width: 768px) {
          margin-top: 15px;
          padding: 12px;
          border-radius: 6px;
        }
        
        @media screen and (max-width: 480px) {
          margin-top: 12px;
          padding: 10px;
          border-radius: 6px;
        }

        small {
          font-size: 0.75rem;
          line-height: 1.4;
          
          @media screen and (max-width: 768px) {
            font-size: 0.8rem;
            line-height: 1.5;
          }
          
          @media screen and (max-width: 480px) {
            font-size: 0.75rem;
            line-height: 1.4;
          }
        }
      }
    }
  }

  // Responsive para panel lateral abierto/cerrado
  .side-panel:not(.open) {
    .voice-settings-section {
      display: none;
    }
  }

  .side-panel.open {
    .voice-settings-section {
      display: block;
      opacity: 1;
      animation: fadeIn 0.3s ease-in;
    }
  }

  // Estilos para la sección de YouTube - Moderno con Tailwind y Material Icons
  .youtube-section {
    padding: 16px;
    
    @media screen and (max-width: 768px) {
      padding: 16px 20px;
    }
    
    @media screen and (max-width: 480px) {
      padding: 16px;
    }

    .youtube-button {
      width: 100%;
      background: #1f2937; /* bg-gray-800 */
      color: #ffffff;
      border: 1px solid #374151; /* border-gray-600 */
      border-radius: 8px; /* rounded-lg */
      padding: 12px 16px;
      font-size: 14px;
      font-weight: 700; /* font-bold */
      font-family: 'Roboto', sans-serif;
      cursor: pointer;
      transition: all 0.3s ease;
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 8px;
      margin-bottom: 12px;
      /* Mejoras responsivas */
      min-height: var(--min-touch-target);
      
      @media screen and (max-width: 768px) {
        min-height: 48px;
        padding: 12px 16px;
        font-size: 14px;
        border-radius: 8px;
      }
      
      @media screen and (max-width: 480px) {
        min-height: 52px;
        padding: 12px 16px;
        font-size: 14px;
        border-radius: 8px;
      }

      // Material Icon para música
      .material-icons {
        font-size: 20px;
      }

      &:hover {
        background: #374151; /* hover:bg-gray-600 */
        transform: translateY(-1px);
        box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
      }

      &:active {
        transform: translateY(0);
      }
      
      &:focus {
        outline: 2px solid #3b82f6;
        outline-offset: 2px;
      }
    }

    .youtube-hint {
      font-size: 12px;
      color: #9ca3af; /* text-gray-400 */
      display: flex;
      align-items: center;
      gap: 8px;
      margin: 0;
      line-height: 1.4;
      
      .material-icons {
        color: #fbbf24; /* text-yellow-400 */
        font-size: 16px;
      }
      
      @media screen and (max-width: 480px) {
        font-size: 11px;
        
        .material-icons {
          font-size: 14px;
        }
      }
    }
  }

  // Selector de indicadores
  .indicators {
    padding: 24px 16px;
    border-bottom: 1px solid #374151; /* border-gray-600 */
    
    .select-container {
      position: relative;
      margin-bottom: 24px;
      
      .dropdown-icon {
        position: absolute;
        right: 12px;
        top: 50%;
        transform: translateY(-50%);
        pointer-events: none;
        color: #9ca3af; /* text-gray-400 */
        
        .material-icons {
          font-size: 24px;
        }
      }
    }
    
    .status-indicator {
      display: flex;
      justify-content: center;
      margin-bottom: 24px;
      
      .status-button {
        background: #374151; /* bg-gray-700 */
        color: #ffffff;
        border: none;
        border-radius: 8px; /* rounded-lg */
        padding: 8px 24px;
        font-size: 14px;
        font-weight: 700; /* font-bold */
        font-family: 'Roboto', sans-serif;
        cursor: pointer;
        transition: all 0.3s ease;
        display: flex;
        align-items: center;
        gap: 8px;
        
        &.connected {
          background: #10b981; /* bg-green-500 */
          
          &:hover {
            background: #059669; /* hover:bg-green-600 */
          }
        }
        
        &:not(.connected) {
          background: #6b7280; /* bg-gray-500 */
          
          &:hover {
            background: #4b5563; /* hover:bg-gray-600 */
          }
        }
        
        .material-icons {
          font-size: 20px;
        }
        
        &:hover {
          transform: translateY(-1px);
          box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
        }
        
        &:active {
          transform: translateY(0);
        }
      }
    }
  }

  // Panel de logging
  .side-panel-container {
    flex: 1;
    overflow-x: hidden;
    overflow-y: auto;
    width: 100%;
    display: block;
    margin-left: 8px;
  }
}

.top {
  width: 100%;
  flex-grow: 0;
  flex-shrink: 0;
  height: 30px;
  display: flex;
  align-self: flex-end;
  align-items: center;
  transition: all 0.2s ease-in;
}

.top button {
  background: transparent;
  border: 0;
  cursor: pointer;
  font-size: 1.25rem;
  line-height: 1.75rem;
  padding: 4px;
}

/* Estilos para overlay móvil del SidePanel */
.side-panel.mobile-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  height: 100dvh;
  background: rgba(0, 0, 0, 0.95);
  z-index: 2000;
  overflow-y: auto;
  padding: 0;
  border: none;
  
  /* Header móvil personalizado */
  .mobile-panel-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 16px 20px;
    background: #1a1a1a;
    border-bottom: 1px solid #333;
    position: sticky;
    top: 0;
    z-index: 1;
    
    h2 {
      color: #ffffff;
      font-size: 18px;
      font-weight: 600;
      margin: 0;
    }
    
    .mobile-close-btn {
      background: #333;
      border: none;
      color: #ffffff;
      width: 36px;
      height: 36px;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 18px;
      cursor: pointer;
      transition: background 0.2s ease;
      
      &:hover {
        background: #555;
      }
    }
  }
  
  /* Ocultar header desktop en móvil */
  .top {
    display: none;
  }
  
  /* Ajustar contenido para móvil */
  .voice-settings-section,
  .youtube-section,
  .indicators,
  .input-container {
    padding: 16px 20px;
    border-bottom: 1px solid #333;
  }
}

/* Estilos móviles para el SidePanel como overlay */
@media screen and (max-width: 768px) {
  .side-panel.mobile-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    height: 100dvh;
    background: rgba(18, 18, 18, 0.95);
    backdrop-filter: blur(10px);
    z-index: 9999;
    padding: 20px;
    box-sizing: border-box;
    overflow-y: auto;
    
    // Header móvil específico
    .mobile-panel-header {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 20px;
      padding-bottom: 15px;
      border-bottom: 1px solid var(--mobile-border, #404040);
      
      h2 {
        color: var(--mobile-text, #ffffff);
        font-size: 18px;
        font-weight: 600;
        margin: 0;
        flex: 1;
        text-align: center;
      }
      
      .mobile-close-btn {
        background: none;
        border: none;
        color: var(--mobile-text, #ffffff);
        font-size: 24px;
        cursor: pointer;
        padding: 8px;
        border-radius: 50%;
        width: 40px;
        height: 40px;
        display: flex;
        align-items: center;
        justify-content: center;
        transition: all 0.2s ease;
        
        &:hover {
          background: rgba(255, 255, 255, 0.1);
          transform: scale(1.1);
        }
        
        &:active {
          transform: scale(0.9);
        }
      }
    }
    
    // Ocultar header desktop en móvil
    .top {
      display: none;
    }
    
    // Ajustar secciones para móvil
    .voice-settings-section,
    .youtube-section {
      margin-bottom: 20px;
      padding: 15px;
      background: rgba(42, 42, 42, 0.8);
      border-radius: 12px;
      border: 1px solid var(--mobile-border, #404040);
      
      .voice-settings-toggle,
      .youtube-button {
        width: 100%;
        padding: 12px 16px;
        background: var(--mobile-accent, #4285f4);
        color: var(--mobile-text, #ffffff);
        border: none;
        border-radius: 8px;
        font-size: 16px;
        font-weight: 500;
        cursor: pointer;
        transition: all 0.2s ease;
        text-align: left;
        
        &:hover {
          background: var(--mobile-accent-hover, #3367d6);
          transform: translateY(-1px);
        }
        
        &:active {
          transform: translateY(0);
        }
      }
      
      .youtube-hint {
        margin-top: 10px;
        font-size: 13px;
        color: var(--mobile-text-secondary, #b3b3b3);
        line-height: 1.4;
      }
    }
    
    // Indicadores móviles
    .indicators {
      margin-bottom: 20px;
      
      .react-select {
        margin-bottom: 15px;
      }
      
      .streaming-indicator {
        font-size: 16px;
        font-weight: 600;
        text-align: center;
        padding: 12px;
        border-radius: 8px;
        background: rgba(42, 42, 42, 0.8);
        border: 1px solid var(--mobile-border, #404040);
      }
    }
    
    // Panel de logs móvil
    .side-panel-container {
      background: rgba(42, 42, 42, 0.8);
      border-radius: 12px;
      padding: 15px;
      margin-bottom: 20px;
      max-height: 200px;
      overflow-y: auto;
      border: 1px solid var(--mobile-border, #404040);
    }
    
    // Input container móvil
    .input-container {
      background: rgba(42, 42, 42, 0.8);
      border-radius: 12px;
      padding: 15px;
      border: 1px solid var(--mobile-border, #404040);
      
      .input-content {
        position: relative;
        
        .input-area {
          width: 100%;
          min-height: 80px;
          padding: 12px;
          border: 1px solid var(--mobile-border, #404040);
          border-radius: 8px;
          background: rgba(18, 18, 18, 0.8);
          color: var(--mobile-text, #ffffff);
          font-size: 16px;
          line-height: 1.4;
          resize: vertical;
          
          &::placeholder {
            color: var(--mobile-text-secondary, #b3b3b3);
          }
          
          &:focus {
            outline: 2px solid var(--mobile-accent, #4285f4);
            border-color: var(--mobile-accent, #4285f4);
          }
        }
        
        .send-button {
          position: absolute;
          bottom: 8px;
          right: 8px;
          background: var(--mobile-accent, #4285f4);
          color: var(--mobile-text, #ffffff);
          border: none;
          border-radius: 6px;
          padding: 8px 12px;
          font-size: 14px;
          cursor: pointer;
          transition: all 0.2s ease;
          
          &:hover {
            background: var(--mobile-accent-hover, #3367d6);
            transform: scale(1.05);
          }
          
          &:active {
            transform: scale(0.95);
          }
        }
      }
    }
  }
}

// Optimizaciones para pantallas muy pequeñas
@media screen and (max-width: 375px) {
  .side-panel.mobile-overlay {
    padding: 15px;
    
    .mobile-panel-header {
      margin-bottom: 15px;
      
      h2 {
        font-size: 16px;
      }
      
      .mobile-close-btn {
        width: 36px;
        height: 36px;
        font-size: 20px;
      }
    }
    
    .voice-settings-section,
    .youtube-section {
      padding: 12px;
      margin-bottom: 15px;
      
      .voice-settings-toggle,
      .youtube-button {
        padding: 10px 14px;
        font-size: 15px;
      }
    }
    
    .side-panel-container {
      max-height: 150px;
      padding: 12px;
    }
    
    .input-container {
      padding: 12px;
      
      .input-area {
        min-height: 60px;
        padding: 10px;
        font-size: 15px;
      }
    }
  }
}
