/**
 * Estilos para notificación de reproducción automática
 */

.autoplay-notification {
  position: fixed;
  top: 20px;
  right: 20px;
  z-index: 99999; /* Más alto para asegurar visibilidad */
  animation: slideInRight 0.5s ease-out, attention 1s infinite alternate;
  
  @media (max-width: 768px) {
    top: 10px;
    right: 10px;
    left: 10px;
  }
}

.autoplay-notification__content {
  background: linear-gradient(135deg, 
    rgba(255, 69, 0, 0.95) 0%, 
    rgba(255, 140, 0, 0.95) 100%);
  border-radius: 12px;
  padding: 20px; /* Más padding para mayor visibilidad */
  display: flex;
  align-items: center;
  gap: 15px;
  box-shadow: 
    0 12px 40px rgba(255, 69, 0, 0.4),
    0 6px 20px rgba(0, 0, 0, 0.3),
    0 0 20px rgba(255, 140, 0, 0.6); /* Glow effect */
  backdrop-filter: blur(15px);
  border: 2px solid rgba(255, 255, 255, 0.4);
  max-width: 400px;
  min-width: 320px;
  position: relative;
  overflow: hidden;
  transform: scale(1.05); /* Ligeramente más grande */
  
  @media (max-width: 768px) {
    padding: 16px;
    min-width: auto;
    max-width: none;
    transform: scale(1);
  }
}

.autoplay-notification__icon {
  flex-shrink: 0;
  
  .music-note-animation {
    font-size: 24px;
    animation: musicPulse 2s infinite ease-in-out;
    
    span {
      display: block;
      transform-origin: center;
    }
  }
}

.autoplay-notification__text {
  flex: 1;
  min-width: 0;
}

.autoplay-notification__title {
  font-size: 12px;
  font-weight: 600;
  color: rgba(255, 255, 255, 0.9);
  text-transform: uppercase;
  letter-spacing: 0.5px;
  margin-bottom: 4px;
}

.autoplay-notification__message {
  font-size: 14px;
  color: #ffffff;
  font-weight: 500;
  line-height: 1.3;
  
  // Truncar texto largo en móviles
  @media (max-width: 480px) {
    font-size: 13px;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
  }
}

.autoplay-notification__progress {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: rgba(255, 255, 255, 0.2);
  overflow: hidden;
  
  .progress-bar {
    height: 100%;
    background: linear-gradient(90deg, 
      #ff6b6b, 
      #ffd93d, 
      #6bcf7f, 
      #4ecdc4, 
      #45b7d1);
    width: 100%;
    animation: progressSlide 5s linear;
    transform: translateX(-100%);
  }
}

.autoplay-notification__close {
  flex-shrink: 0;
  background: none;
  border: none;
  color: rgba(255, 255, 255, 0.7);
  font-size: 18px;
  cursor: pointer;
  padding: 4px;
  border-radius: 4px;
  transition: all 0.2s ease;
  
  &:hover {
    background: rgba(255, 255, 255, 0.1);
    color: #ffffff;
    transform: scale(1.1);
  }
  
  &:active {
    transform: scale(0.95);
  }
}

// Animaciones
@keyframes slideInRight {
  from {
    transform: translateX(100%);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}

@keyframes musicPulse {
  0%, 100% {
    transform: scale(1) rotate(0deg);
  }
  25% {
    transform: scale(1.1) rotate(-5deg);
  }
  50% {
    transform: scale(1.05) rotate(0deg);
  }
  75% {
    transform: scale(1.1) rotate(5deg);
  }
}

@keyframes progressSlide {
  from {
    transform: translateX(-100%);
  }
  to {
    transform: translateX(0);
  }
}

// Variante para tema oscuro
.dark-theme .autoplay-notification__content {
  background: linear-gradient(135deg, 
    rgba(30, 30, 30, 0.95) 0%, 
    rgba(45, 45, 45, 0.95) 100%);
  border: 1px solid rgba(255, 255, 255, 0.1);
}

// Efectos adicionales para mejorar visibilidad
.autoplay-notification {
  animation-duration: 0.5s;
  animation-fill-mode: both;
  
  &.autoplay-notification--exit {
    animation-name: slideOutRight;
    animation-duration: 0.3s;
  }
}

@keyframes slideOutRight {
  from {
    transform: translateX(0);
    opacity: 1;
  }
  to {
    transform: translateX(100%);
    opacity: 0;
  }
}

// Animación de atención para mayor visibilidad
@keyframes attention {
  0% {
    transform: scale(1.05);
    box-shadow: 
      0 12px 40px rgba(255, 69, 0, 0.4),
      0 6px 20px rgba(0, 0, 0, 0.3),
      0 0 20px rgba(255, 140, 0, 0.6);
  }
  100% {
    transform: scale(1.08);
    box-shadow: 
      0 15px 50px rgba(255, 69, 0, 0.6),
      0 8px 25px rgba(0, 0, 0, 0.4),
      0 0 30px rgba(255, 140, 0, 0.8);
  }
}

// Responsive para pantallas muy pequeñas
@media (max-width: 360px) {
  .autoplay-notification__content {
    padding: 10px;
    gap: 8px;
  }
  
  .autoplay-notification__icon .music-note-animation {
    font-size: 20px;
  }
  
  .autoplay-notification__message {
    font-size: 12px;
  }
}
