// Responsive variables
:root {
  --conversation-button-width-desktop: 160px;
  --conversation-button-width-tablet: 150px;
  --conversation-button-width-mobile: 140px;
  --conversation-button-height-desktop: auto;
  --conversation-button-height-mobile: 48px;
  --conversation-button-font-size-desktop: 0.9rem;
  --conversation-button-font-size-mobile: 0.8rem;
  --conversation-button-padding-desktop: 12px 20px;
  --conversation-button-padding-mobile: 10px 16px;
  --conversation-icon-size-desktop: 1.1rem;
  --conversation-icon-size-mobile: 1rem;
}

.conversation-control-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;
  margin: 10px;
  // Touch scrolling optimization
  -webkit-overflow-scrolling: touch;
  
  @media (max-width: 768px) {
    /* Compacto en móviles - menor margen */
    margin: 4px;
    gap: 4px;
    /* Ocultar completamente indicadores de estado en móviles */
    .status-indicators {
      display: none !important;
      visibility: hidden !important;
      opacity: 0 !important;
      position: absolute !important;
      top: -9999px !important;
    }
  }

  .conversation-control-button {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 8px;
    padding: var(--conversation-button-padding-desktop);
    border: none;
    border-radius: 25px;
    font-size: var(--conversation-button-font-size-desktop);
    font-weight: 600;
    cursor: pointer;
    transition: all 0.3s ease;
    min-width: var(--conversation-button-width-desktop);
    min-height: var(--conversation-button-height-desktop);
    position: relative;
    overflow: hidden;
    // Touch optimizations
    -webkit-tap-highlight-color: transparent;
    touch-action: manipulation;

    @media (max-width: 768px) {
      /* Más compacto en móviles */
      padding: var(--conversation-button-padding-mobile);
      font-size: var(--conversation-button-font-size-mobile);
      min-width: var(--conversation-button-width-mobile);
      min-height: var(--conversation-button-height-mobile);
      gap: 6px;
      border-radius: 20px;
      
      /* Solo mostrar icono en pantallas muy pequeñas */
      @media (max-width: 480px) {
        .button-text {
          display: none;
        }
        min-width: 44px;
        padding: 12px;
      }
    }

    // Estado por defecto (escuchando)
    background: linear-gradient(135deg, #28a745, #20c997);
    color: white;
    box-shadow: 0 2px 8px rgba(40, 167, 69, 0.3);

    .button-icon {
      font-size: var(--conversation-icon-size-desktop);
      animation: pulse 2s infinite;
    }

    .button-text {
      font-weight: 600;
      letter-spacing: 0.5px;
    }

    // Hover states for desktop
    @media (hover: hover) {
      &:hover:not(:disabled) {
        transform: translateY(-2px);
        box-shadow: 0 4px 12px rgba(40, 167, 69, 0.4);
      }
    }

    &:active:not(:disabled) {
      transform: translateY(0);
    }

    // Estado cuando puede interrumpir (AI hablando)
    &.can-interrupt {
      background: linear-gradient(135deg, #dc3545, #e74c3c);
      box-shadow: 0 2px 8px rgba(220, 53, 69, 0.3);
      animation: urgentPulse 1.5s infinite;

      @media (hover: hover) {
        &:hover {
          background: linear-gradient(135deg, #c82333, #dc3545);
          box-shadow: 0 4px 12px rgba(220, 53, 69, 0.5);
        }
      }

      .button-icon {
        animation: shake 0.5s infinite;
      }
    }

    // Estado hablando
    &.speaking {
      background: linear-gradient(135deg, #ffc107, #ffca2c);
      color: #333;
      box-shadow: 0 2px 8px rgba(255, 193, 7, 0.3);

      .button-icon {
        animation: speaking 1s infinite ease-in-out;
      }
    }

    // Estado interrumpido
    &.interrupted {
      background: linear-gradient(135deg, #6f42c1, #8e44ad);
      color: white;
      box-shadow: 0 2px 8px rgba(111, 66, 193, 0.3);

      .button-icon {
        animation: checkmark 0.6s ease-out;
      }
    }

    // Estado desconectado
    &.disconnected {
      background: linear-gradient(135deg, #6c757d, #868e96);
      color: white;
      box-shadow: 0 2px 8px rgba(108, 117, 125, 0.3);
      cursor: not-allowed;

      .button-icon {
        animation: none;
      }
    }

    &:disabled {
      opacity: 0.6;
      cursor: not-allowed;
      transform: none !important;

      .button-icon {
        animation: none;
      }
    }

    // Efecto de ondas al hacer clic
    &::before {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      width: 0;
      height: 0;
      border-radius: 50%;
      background: rgba(255, 255, 255, 0.3);
      transform: translate(-50%, -50%);
      transition: width 0.3s, height 0.3s;
    }

    &:active:not(:disabled)::before {
      width: 100px;
      height: 100px;
    }
  }    // Indicadores de estado
    .status-indicators {
      display: flex;
      flex-direction: column;
      gap: 4px;
      min-height: 60px;

      .status-indicator {
        display: flex;
        align-items: center;
        gap: 6px;
        padding: 4px 8px;
        border-radius: 12px;
        font-size: clamp(0.65rem, 2vw, 0.75rem);
        font-weight: 500;
        animation: slideIn 0.3s ease-out;
        // Better touch interaction
        min-height: 32px;

        .indicator-dot {
          width: clamp(6px, 2vw, 8px);
          height: clamp(6px, 2vw, 8px);
          border-radius: 50%;
          animation: dot-pulse 1.5s infinite;
          flex-shrink: 0;
        }

        .indicator-text {
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
        }

      &.ai-speaking {
        background: rgba(255, 193, 7, 0.1);
        color: #ffc107;
        border: 1px solid rgba(255, 193, 7, 0.3);

        .indicator-dot {
          background: #ffc107;
        }
      }

      &.voice-speaking {
        background: rgba(23, 162, 184, 0.1);
        color: #17a2b8;
        border: 1px solid rgba(23, 162, 184, 0.3);

        .indicator-dot {
          background: #17a2b8;
        }
      }

      &.interrupted {
        background: rgba(111, 66, 193, 0.1);
        color: #6f42c1;
        border: 1px solid rgba(111, 66, 193, 0.3);

        .indicator-dot {
          background: #6f42c1;
          animation: checkmark-dot 0.6s ease-out;
        }
      }
    }
  }
}

// Animaciones
@keyframes pulse {
  0%, 100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
}

@keyframes urgentPulse {
  0%, 100% {
    box-shadow: 0 2px 8px rgba(220, 53, 69, 0.3);
  }
  50% {
    box-shadow: 0 4px 16px rgba(220, 53, 69, 0.6);
  }
}

@keyframes shake {
  0%, 100% { transform: translateX(0); }
  25% { transform: translateX(-2px); }
  75% { transform: translateX(2px); }
}

@keyframes speaking {
  0%, 100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.05);
  }
}

@keyframes checkmark {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
  }
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes dot-pulse {
  0%, 100% {
    opacity: 1;
    transform: scale(1);
  }
  50% {
    opacity: 0.5;
    transform: scale(0.8);
  }
}

@keyframes checkmark-dot {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.5);
  }
  100% {
    transform: scale(1);
  }
}

// Responsive design - Tablet
@media screen and (max-width: 1024px) {
  :root {
    --conversation-button-width-desktop: var(--conversation-button-width-tablet);
    --conversation-button-font-size-desktop: 0.85rem;
    --conversation-icon-size-desktop: 1.05rem;
  }

  .conversation-control-container {
    margin: 8px;
  }
}

// Responsive design - Mobile
@media screen and (max-width: 768px) {
  :root {
    --conversation-button-width-desktop: var(--conversation-button-width-mobile);
    --conversation-button-height-desktop: var(--conversation-button-height-mobile);
    --conversation-button-font-size-desktop: var(--conversation-button-font-size-mobile);
    --conversation-button-padding-desktop: var(--conversation-button-padding-mobile);
    --conversation-icon-size-desktop: var(--conversation-icon-size-mobile);
  }

  .conversation-control-container {
    gap: 6px;
    margin: 6px;

    .conversation-control-button {
      border-radius: 20px;
      width: 100%;
      max-width: var(--conversation-button-width-mobile);
    }

    .status-indicators {
      min-height: 50px;
      gap: 3px;

      .status-indicator {
        padding: 3px 6px;
        border-radius: 10px;
        min-height: 28px;

        .indicator-text {
          font-size: 0.65rem;
        }
      }
    }
  }
}

// Extra small screens
@media screen and (max-width: 480px) {
  .conversation-control-container {
    .conversation-control-button {
      min-width: 120px;
      padding: 8px 12px;
      font-size: 0.75rem;
      border-radius: 18px;

      .button-icon {
        font-size: 0.95rem;
      }

      .button-text {
        letter-spacing: 0.3px;
      }
    }

    .status-indicators {
      min-height: 40px;

      .status-indicator {
        padding: 2px 4px;
        font-size: 0.6rem;
        min-height: 24px;

        .indicator-dot {
          width: 5px;
          height: 5px;
        }
      }
    }
  }
}

// Touch-specific optimizations
@media (hover: none) and (pointer: coarse) {
  .conversation-control-container {
    .conversation-control-button {
      min-height: 48px;

      // Touch states for mobile
      &:active:not(:disabled) {
        transform: scale(0.98);
      }

      &.can-interrupt:active:not(:disabled) {
        background: linear-gradient(135deg, #bd2130, #c82333);
      }

      &.speaking:active:not(:disabled) {
        background: linear-gradient(135deg, #e0a800, #ffc107);
      }

      &.interrupted:active:not(:disabled) {
        background: linear-gradient(135deg, #5a32a3, #6f42c1);
      }
    }

    .status-indicators {
      .status-indicator {
        min-height: 36px;
      }
    }
  }
}

// Tema oscuro
@media (prefers-color-scheme: dark) {
  .conversation-control-container {
    .conversation-control-button {
      &.speaking {
        color: #000;
      }
    }

    .status-indicators {
      .status-indicator {
        &.ai-speaking {
          background: rgba(255, 193, 7, 0.15);
          border-color: rgba(255, 193, 7, 0.4);
        }

        &.voice-speaking {
          background: rgba(23, 162, 184, 0.15);
          border-color: rgba(23, 162, 184, 0.4);
        }

        &.interrupted {
          background: rgba(111, 66, 193, 0.15);
          border-color: rgba(111, 66, 193, 0.4);
        }
      }
    }
  }
}

// Accesibilidad y mejoras adicionales
@media (prefers-reduced-motion: reduce) {
  .conversation-control-container {
    .conversation-control-button {
      animation: none !important;
      transition: background-color 0.2s ease, box-shadow 0.2s ease;

      .button-icon {
        animation: none !important;
      }

      &::before {
        transition: none;
      }

      &:hover:not(:disabled) {
        transform: none;
      }
    }

    .status-indicators {
      .status-indicator {
        animation: none !important;

        .indicator-dot {
          animation: none !important;
        }
      }
    }
  }
}

// High contrast mode support
@media (prefers-contrast: high) {
  .conversation-control-container {
    .conversation-control-button {
      border: 2px solid transparent;

      &.can-interrupt {
        border-color: #dc3545;
      }

      &.speaking {
        border-color: #ffc107;
        color: #000;
      }

      &.interrupted {
        border-color: #6f42c1;
      }

      &.disconnected {
        border-color: #6c757d;
      }
    }

    .status-indicators {
      .status-indicator {
        border: 1px solid currentColor;
      }
    }
  }
}

// Focus improvements for keyboard navigation
.conversation-control-container {
  .conversation-control-button:focus-visible {
    outline: 2px solid #fff;
    outline-offset: 2px;
  }
}
